<template>
    <div class="container my-5">
        <!--contact area start-->
        <div class="contact_area">
            <div class="container">
                <div class="row">
                    <div class="col-lg-8 col-md-12 mb-2">
                        <div class="contact_message form">
                            <div class="section_title">
                                <h2><span>{{ "contact.sendQuestionAndSuggestion"| translate }}</span></h2>
                            </div>
                            <form class="row">
                                <p class="col-lg-6 col-md-12">
                                    <label> {{ "contact.fullName"| translate }}</label>
                                    <input name="name" v-model="issue.full_name" type="text">
                                </p>
                                <p class="col-lg-6 col-md-12">
                                    <label> {{ "contact.email"| translate }}</label>
                                    <input name="email" v-model="issue.raised_by" type="email">
                                </p>
                                <p class="col-12">
                                    <label> {{ "contact.subject"| translate }}</label>
                                    <input name="subject" v-model="issue.subject" type="text">
                                </p>
                                <div class="contact_textarea col-12">
                                    <label> {{ "contact.message"| translate }}</label>
                                    <textarea name="message" v-model="issue.description" class="form-control2"></textarea>
                                </div>
                                <div class="col-12 text-right">
                                    <button @click="addIssue"> {{ "contact.send"| translate }}</button>
                                </div>
                                <p class="form-messege"></p>
                            </form>

                        </div>
                    </div>
                    <div class="col-lg-4 col-md-12">
                        <div class="contact_message content">
                            <div class="section_title">
                                <h2><span>{{ "contact.ourStore"| translate }}</span></h2>
                            </div>
                            <p v-html="data"/>
                        </div>
                    </div>
                </div>
            </div>
        </div>

        <!--contact area end-->
    </div>
</template>

<script>
import apiRequest from '@/helper/apiRequest.js'

export default {
    created () {
        this.$store.state.showLoading = false
        this.fetchData()
    },
    data() {
        return {
            data: '',
            issue: {
                subject: '',
                raised_by: '',
                description: '',
                full_name: ''
            }
        }
    },
    methods: {
        async fetchData () {
            this.$store.state.showLoading = true
            var body = {
                'identifier': 'GET Our Store'
            }
            var response = await apiRequest.requestRead(body)
            if(response.data != null){
                if (response.data.length > 0) {
                    var datas = response.data[0].our_store.replace("table-bordered","table-borderless").replace(/<td/g,"<td class='p-0'")
                    this.data = datas
                }
            }
            else {
                this.$notify({
                    group: 'notification',
                    type: 'danger',
                    title: 'Oops...',
                    text: response.error
                });
            }
            this.$store.state.showLoading = false
        },
        async addIssue (e) {
            e.preventDefault()
            if (this.issue.full_name == '') {
                this.$notify({
                    group: 'notification',
                    type: 'danger',
                    title: 'Oops...',
                    text: 'contact.fullNameRequired'
                });
                return
            }
            else if (this.issue.raised_by == '') {
                this.$notify({
                    group: 'notification',
                    type: 'danger',
                    title: 'Oops...',
                    text: 'contact.emailRequired'
                });
                return
            }
            else if (this.issue.subject == '') {
                this.$notify({
                    group: 'notification',
                    type: 'danger',
                    title: 'Oops...',
                    text: 'contact.subjectRequired'
                });
                return
            }
            else if (this.issue.description == '') {
                this.$notify({
                    group: 'notification',
                    type: 'danger',
                    title: 'Oops...',
                    text: 'contact.descRequired'
                });
                return
            }
            this.$store.state.showLoading = true
            var body = {
                'subject': this.issue.subject,
                'raised_by': this.issue.raised_by,
                'description': this.issue.description,
                'full_name': this.issue.full_name,
                'doctype': 'Issue'
            }
            var response = await apiRequest.createDoc(body)
            if(response.data != null){
                this.$notify({
                    group: 'notification',
                    type: 'success',
                    title: 'Yey...',
                    text: 'notification.weHaveReceivedYourQuestion'
                });
                this.issue = {
                    subject: '',
                    raised_by: '',
                    description: '',
                    full_name: ''
                }
            }
            else {
                this.$notify({
                    group: 'notification',
                    type: 'danger',
                    title: 'Oops...',
                    text: response.error
                });
            }
            this.$store.state.showLoading = false
        },
    }
}
</script>