
import axios from 'axios'
import cookies from 'vue-cookies'
import store from '../store'

var apiRequest = {}

apiRequest.login = async (body) => {
  console.log(cookies.get('sid'))
  var url = store.state.baseURL+'/api/method/login'
  var response = await axios.get(url + '?usr=' + body['usr'] + '&pwd=' + body['pwd'])
      .then(response => {
        // return {'data': {response}}
        var rsp = apiRequest.checkResponseLogin(response,url)
        return rsp
      })
      .catch(error => {
        console.log({'error':error})
        return {'error': 'Invalid email or password'}
      })
  return response
};

apiRequest.resetPwd = async (body) => {
  var url = store.state.baseURL+'/api/method/commerce.user_integration.api.api_reset_password.reset_password'
  var head = {
    'content-type': 'application/text'
  }
  if (apiRequest.isGuest()) {
    head['authorization'] = store.state.guestToken
  }
  var response = await axios.post(url, body, {
      headers: head
      })
      .then(response => {
        var rsp = apiRequest.checkResponse(response,url)
        return rsp
      })
      .catch(error => {
        return {'error':error}
      })
  return response
};

