apiRequest.likeItem = async (context, item, like) => {
  var url = store.state.baseURL+'/api/method/frappe.desk.like.toggle_like'
  var body = {
    "doctype": "Item",
    "name": item,
    "add": like ? 'Yes' : 'No'
  }
  if (apiRequest.isGuest()) {
    context.$notify({
      group: 'notification',
      type: 'danger',
      title: 'Oops...',
      text: 'notification.pleaseLogin'
    });
    return false
  }
  var response = await axios.post(url, body, {
      })
      .then(response => {
        var rsp = apiRequest.checkFrappeResponse(response)
        if(rsp.data != null){
          apiRequest.getWishlistBadge()
          context.$notify({
              group: 'notification',
              type: 'success',
              title: 'Yey...',
              text: like ? 'notification.successfullyAddedItemToWishlist' : 'notification.successfullyRemoveItemFromWishlist'
          });
          return true
        } else {
          context.$notify({
              group: 'notification',
              type: 'danger',
              title: 'Oops...',
              text: rsp.error
          });
          return false
        }
      })
      .catch(error => {
        console.log({'error':error})
        return false
      })
  return response
};

