apiRequest.checkResponseLogin = (response, url) => {
  console.log(response.status+': '+url)
  if (response.status == 200){
    console.log('RESPONSE', response)
    if (response.data.message){
      return {'data': response.data}
    }
    else {
      return {'error': 'Invalid email or password'}
    }
  }
  else {
    return {'error': 'Server Error'}
  }
}

apiRequest.checkResponse = (response, url) => {
  console.log(response.status+': '+url)
  if (response.status == 200){
    var data = response.data.message
    if (data.code == 200){
      return {'data': data.data}
    }
    else {
      return {'error': data.error}
    }
  }
  else {
    return {'error': 'Server Error'}
  }
}

apiRequest.checkFrappeResponse = (response) => {
  if (response.status == 200){
    return {'data': ''}
  }
  else {
    return {'error': 'Server Error'}
  }
}

apiRequest.isGuest = () => {
  // if (cookies.get('sid') == 'Guest' || cookies.get('sid') == null || cookies.get('sid') == undefined){
  console.log('-- FULLNAME --', store.state.fullname)
  var isAlreadyLogin = localStorage.getItem('isAlreadyLogin') || '0'
  if ((store.state.fullname == '' || store.state.fullname == 'API WSS') && isAlreadyLogin == '0' ) {
    return true
  }
  else {
    return false
  }
}

apiRequest.logout = async () => {
  if (!apiRequest.isGuest()) {
    var url = store.state.baseURL+'/api/method/logout'
    var response = await axios.get(url)
      .then(response => {
        cookies.remove('PHPSESSID')
        return {'data': {response}}
      })
      .catch(error => {
        console.log({'error':error})
        return {'error': 'Invalid email or password'}
      })
    console.log(response)
    return response
  }
}

export default apiRequest;