<template>
    <div class="row text-center align-items-center m-0 stepper">
        <div class="col-3 px-0 my-1">
            <div class="stepper-button" @click="decrement" :style="canMin ? '' : 'color: ' + disableBgColor + ';'">-</div>
        </div>
        <div class="col-6 px-0">
            <input
                class="ml-0 no-border"
                style="width: 100%; border: 0px; text-align: center !important"
                type="number"
                :readonly="disableEdit !== undefined"
                v-model="value">
        </div>
        <div class="col-3 px-0 my-1">
            <div class="stepper-button" :style="canMax ? '' : 'color: ' + disableBgColor + ';'" @click="increment">+</div>
        </div>
    </div>
</template>

<style>
/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
  text-align: center;
}

.stepper {
    border: 1px solid #ddd;
    border-radius: 5px;
}

.stepper-button {
    cursor: pointer;
}
</style>

<script>
export default {
  props: [
    'minimum',
    'maximum',
    'current',
    'disableEdit',
    'disabledColor',
    'multiplication'
  ],
  data () {
    return {
      number: 0,
      canMin: false,
      canMax: false
    }
  },
  computed: {
    disableBgColor () {
      return this.disabledColor || '#ddd'
    },
    value:  {
      get () { 
        return this.number
      },
      set (val) {
        this.$emit('model', val)
        console.log(`set stepper model value ${val}`)
        var num = parseInt(val)
        this.number = isNaN(num) ? 0 : num
      }
    }
  },
  created () {
    // var multiplication = this.multiplication || 1
    var minimum = this.minimum || 1
    this.value = Math.max(this.current, minimum)
    // this.value = (this.current || 1) * (this.multiplication || 1)
    this.validate()
  },
  mounted () {
    this.validate()
  },
  methods: {
    validate () {
      var min = this.minimum || 1
      // var min = (this.minimum || 1) * this.multiplication
      var max = this.maximum || 99999999

      this.canMin = this.value > min
      this.canMax = this.value < max
    },
    decrement () {
      if (this.canMin) {
        this.value -= 1 * (this.multiplication || 1)
        this.validate()
      }
    },
    increment () {
      if (this.canMax) {
        this.value += 1 * (this.multiplication || 1)
        this.validate()
      }
    }
  }
}
</script>