<template>
  <div>
    <!--blog body area start-->
    <div class="blog_details blog_padding mt-23">
        <div class="container">
            <div class="row">
                <div class="col-lg-9 col-md-12">
                    <!--blog grid area start-->
                    <div class="blog_details_wrapper">
                        <div class="blog_thumb">
                            <a href="#"><img class="card" :src="$store.state.baseURL + encodeURIComponent(dataBlog.image)" alt=""></a>
                        </div>
                        <div class="blog_content">
                            <h3 class="post_title">{{ dataBlog.title || ''}}</h3>
                            <div class="post_meta">
                                <span><i class="ion-person mr-1"></i> {{ "blog.postby" | translate }} </span>
                                <span>{{ dataBlog.modified_by || ''}}</span>
                                <span class="mx-2">|</span>
                                <span><i class="fa fa-calendar mr-1" aria-hidden="true"></i>  {{ "blog.poston" | translate }}  {{ dataBlog.published_on | moment('DD MMMM YYYY')}}	</span>

                            </div>
                            <div class="post_content">
                                <p v-html="dataBlog.content"></p>
                            </div>
                        </div>

                    </div>
                    <!--blog grid area start-->
                </div>
                <div class="col-lg-3 col-md-12">
                    <div class="blog_sidebar_widget">
                        <div class="widget_list widget_post">
                            <h3>{{ "blog.recent" | translate }}</h3>
                            <div class="post_wrapper" v-for="(data, index) in dataRecent" :key="index">
                                <!-- <router-link :to="'/blog/' + data.name"> -->
                                    <div class="row ishover" @click="goToDetailBlog(data.name)">
                                        <div class="col-5">
                                            <a><img :src="$store.state.baseURL + encodeURIComponent(data.image)" alt="" :style="imageHeight > 0 ? 'height: ' + imageHeight + 'px' : ''" id="recent"></a>
                                        </div>
                                        <div class="post_info col-7">
                                            <h3><b>{{ data.title || ''}}</b></h3>
                                            <span>{{ data.published_on | moment('DD MMMM YYYY')}}</span>
                                        </div>
                                    </div>
                                <!-- </router-link> -->
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
    <!--blog section area end-->
  </div>
</template>

<script>
import apiRequest from '@/helper/apiRequest.js'
import helper from '@/helper/global.js'

export default {
  name: 'Blog',
  components: { 
      
   },
  data () {
    return {
      dataBlog: {},
      dataRecent: [],
      imageHeight: 0
    }
  },
  created () {
      window.addEventListener('resize', this.resize),
      this.fetchBlog(),
      this.fetchRecent()
  },
  mounted(){
    this.resize()
  },
  methods: {
    async fetchBlog() {
        this.$store.state.showLoading = true
        var body = {
            "identifier" : "Get Detail Blog Post",
            "name": this.$route.params.id,
            "page": 0
        }
        var response = await apiRequest.requestRead(body)
        if(response.data != null){
            if(response.data.length > 0){
                this.dataBlog = response.data[0]
            }
        }
        else {
            this.$notify({
                group: 'notification',
                type: 'danger',
                title: 'Oops...',
                text: response.error
            });
        }
        this.$store.state.showLoading = false
    },
    async fetchRecent() {
        var body = {
            "identifier" : "Get Blog Post",
            "page": 0
        }
        var response = await apiRequest.requestRead(body)
        if(response.data != null){
            if(response.data.length > 0){
                for(let i in response.data){
                    this.dataRecent.push(response.data[i])
                }
            }
        }
        else {
            this.$notify({
                group: 'notification',
                type: 'danger',
                title: 'Oops...',
                text: response.error
            });
        }
    },
    resize() {
      this.imageHeight = helper.resizeElement('recent', 1)
    },
    goToDetailBlog(name) {
        location.href = '/#/blog/'+name
        location.reload()
    }
  }
}
</script>
