<template>
  <div style="margin-bottom: -10px;">
      <iframe v-if="isValidUrl" style="width: 100%; min-height: 100vh;" :src="url" onload="checkPaymentUrl(this.contentWindow.location)" />
        <!--error section area start-->
        <div class="error_section" v-else>
            <div class="container">
                <div class="error_form">
                    <!-- <h1>404</h1> -->
                    <img src="img/nodata/404.jpg" width="100%" style="max-width: 400px;">
                    <h2 class="mt-4"><b>Opps! Payment URL Not Valid.</b></h2>
                    <p>Sorry but the payment you are looking for does not exist or is temporarily unavailable.</p>
                </div>
            </div>
        </div>
        <!--error section area end-->
  </div>
</template>

<style>

</style>

<script>
import apiRequest from '@/helper/apiRequest.js'

export default {
  name: 'Payment',
  components: { 
      
    },
  data () {
    return {
        url: '',
        isValidUrl: true,
    }
  },
  created () {
    this.checkCookies()
    apiRequest.getCartBadge()
    this.$store.state.showLoading = false
    var route = decodeURIComponent(this.$route.query.uri)
    if(route.startsWith("https://invoice-staging.xendit.co/web/invoices/") || route.startsWith("https://checkout-staging.xendit.co/web/") || route.startsWith("https://checkout.xendit.co/web")) {
        this.url = route
        this.isValidUrl = true
    }
    else {
        this.isValidUrl = false
    }
  },
  methods: {
    checkCookies() {
      if (apiRequest.isGuest()) {
        location.href = '/'
      }
    },
    checkPaymentUrl (data) {
      console.log(data)
    }
  }
}
</script>
