var helper = {}

helper.resizeElement = (elementID, ratio) => {
    var el = document.getElementById(elementID)

    var height = 0
    // console.log('element #' + elementID)
    if (el) {
        height = el.offsetWidth * ratio
        console.log('Element #' + elementID +  ' width: ' + el.offsetWidth)
    }

    return height
}

helper.getElementHeight = (elementID) => {
    var el = document.getElementById(elementID)

    var height = 0
    if (el) {
        height = el.offsetHeight
    }

    return height
}

helper.calculateDiscount = (discountData, amount) => {
    if (discountData == null) {
        return amount
    }

    //COUNT DISC
    var value = discountData.discount_percentage_or_amount
    var price = amount
    var maxDisc = discountData.maximal_discount

    var result = price
    // TODO
    // Check jika ada flash sale dan masih berlangsung
    // maka ambil dari field discount_flash_sale langsung ditampilkan

    if (discountData.discount_type == "Override") {
        result = value
    }
    else if (discountData.discount_type == "Discount Percentage") {
        var disc = price * value / 100.0
        if (disc > maxDisc && maxDisc != 0.0) {
            result = price - discountData.maximal_discount
        }
        else {
            result = price - disc
        }
    }
    else if (discountData.discount_type == "Discount Amount") {
        result = price - value
    }
    else {
        result = 0.0
    }

    return result
}

export default helper;