<script>
import { isMobile } from 'mobile-device-detect'
import apiRequest from '@/helper/apiRequest.js'
// import helper from '@/helper/global.js'

export default {
  name: 'App',
  data() {
      return {
        activeSidebar: [],
        query: "",
        emailSubscribe: '',
        suggestions: [
            {
            name: "Product",
            data: [
                ]
            }
        ],
        sectionConfigs: {
            Product: {
                label: "Product",
                onSelected: selected => {
                    this.goToDetail(selected.item.id)
                }
            },
            default: {
                onSelected: selected => {
                    this.goToDetail(selected.item.id)
                }
            }
        },
        paymentHeight: 0,
        paymentMethod: [],
        itemCategory: [],
        isBrowseCategoriesOpened: false,
        mediaSocial: {},
        aboutUs: ''
      }
  },
  created () {
    this.getCustomer()
    this.checkNavType()
    this.$store.state.isMobile = isMobile

    this.fetchBadge()

    if (localStorage.getItem('language')) {
        this.changeLang(localStorage.getItem('language'))
    }

    window.addEventListener('resize', this.resize)

    this.fetchItemCategory()
    this.fetchPaymentCategory()
    this.fetchMediaSocial()
    this.fetchAboutUs()

    setTimeout(() => {
      this.showLoading = false
      this.resize()
    }, 1000);
  },
  computed: {
    activateOverlay () {
        var overlay = false
        for (var key in this.$store.state.sidebar) {
            overlay = overlay || this.$store.state.sidebar[key]
            // if (this.$store.state.sidebar[key]) {
            //     this.activeSidebar.push(key)
            // }
        }

        return overlay
    },
    isGuest () {
        return apiRequest.isGuest()
    }
  },
  methods: {
    async getCustomer () {
        var body = {
            'identifier': 'Get User Detail'
        }
        var response = await apiRequest.requestRead(body)
        if(response.data != null){
            // Get customer data
            console.log("user",response.data)

            if (response.data.length > 0) {
                if (response.data[0].full_name != '' && response.data[0].full_name != 'API WSS') {
                    localStorage.setItem('isAlreadyLogin', "1")
                    this.$store.state.fullname = response.data[0].full_name
                    this.$store.state.email = response.data[0].name
                } else {
                    this.resetCookies()
                }
            } else {
                this.resetCookies()
            }
        }
        else {
            this.resetCookies()
        }
    },
    resetCookies () {
        var last_state = ''
        this.$cookies.remove('PHPSESSID')
        if (localStorage.getItem('isAlreadyLogin')) {
            last_state = localStorage.getItem('isAlreadyLogin')
        }

        localStorage.setItem('isAlreadyLogin', "0")
        this.$store.state.fullname = ''
        this.$store.state.email = ''

        // expire
        if (last_state == '1') {
            location.reload()
        }
    },
    openCategory (data) {
        this.isBrowseCategoriesOpened = false
        localStorage.setItem('browseCategory', data)
        
        if (this.$route.name == 'shop') {
            location.reload()
        } else {
            this.$router.push('/shop')
        }
    },
    changeLang (lang) {
      this.$store.dispatch("changeCulture", lang)
      localStorage.setItem('language', lang)
    },
    getSuggestionValue(suggestion) {
      console.log("SUGEST VAL")
      let { name, item } = suggestion
      return name == "Ticket" ? item.item_name : item.name
    },
    renderSuggestion(suggestion) {
        console.log("SUGEST VAL",suggestion)
      if (suggestion.name == "Product") {
        const image = suggestion.item;
        return (
          <div style="font-weight: 600; text-overflow: ellipsis; overflow: hidden; white-space: nowrap;">
            <img class={{ avatar: true }} src={this.$store.state.baseURL + image.img} />
            {image.name}
          </div>
        );
      } else {
        return suggestion.item.name
      }
    },
    async fetchResults() {
        // var body = {
        //     "identifier" : "Get Item",
        //     "item_group" : "%",
        //     "new_arrival" : "%",
        //     "popular" : "%",
        //     "page" : 0,
        //     "limit" : 5,
        //     "search": this.query
        // }
        var  body = {
                "search" : this.query,
                "page" : 0,
                "flash_sale" : "",
                "item_group" : "",
                "brand" : "",
                "popular" : "%",
                "new_arrival" : "%",
                "min_price" : 0,
                "max_price" : 1000000000,
                "limit" : 20
            }
        var response = await apiRequest.getItems(body)
        console.log('RESPONSE ITEM',response.data)
        this.suggestions = []
        var datas = []
        for( let i in response.data ) {
            var detail = {
                "name": response.data[i].item_name,
                "id": response.data[i].item_code,
                "img": response.data[i].image
            }
            datas.push(detail)
        }
        if(response.data != null){
            this.suggestions.push({ "name": "Product","data": datas})
        }
    },
    async fetchAboutUs () {
        this.$store.state.showLoading = true
        var body = {
            'identifier': 'GET About Us'
        }
        var response = await apiRequest.requestRead(body)
        if(response.data != null){
            if (response.data.length > 0) {
                this.aboutUs = response.data[0].about_us
            }
        }
        else {
            this.$notify({
                group: 'notification',
                type: 'danger',
                title: 'Oops...',
                text: response.error
            });
        }
        this.$store.state.showLoading = false
    },
    async fetchBadge() {
        apiRequest.getCartBadge()
        apiRequest.getWishlistBadge()
    },
    async fetchItemCategory() {
        var body = {
            "identifier" : "Get Item Category",
            "limit": 9999
        }
        var response = await apiRequest.requestRead(body)
        if(response.data != null){
            this.itemCategory = response.data
        }
    },
    async fetchPaymentCategory() {
        var body = {
            "identifier" : "GET Xendit Payment Methods",
            "limit": 9999
        }
        var response = await apiRequest.requestRead(body)
        if(response.data != null){
            this.paymentMethod = response.data
        }
    },
    async addSubscriber() {
        if (this.emailSubscribe == ''){
            this.$notify({
                group: 'notification',
                type: 'danger',
                title: 'Oops...',
                text: 'notification.pleaseEnterYourEmail'
            });
            return;
        }
        this.$store.state.showLoading = true
        var bodyFormData = new FormData();
        bodyFormData.set('name', 'Subscriber Website');
        bodyFormData.set('email_list', this.emailSubscribe);
        var response = await apiRequest.addSubscriber(bodyFormData)
        if(response.data != null){
            this.$notify({
                group: 'notification',
                type: 'success',
                title: 'Yey...',
                text: 'notification.thankYouForSubscribe'
            });
            this.emailSubscribe = ''
        }
        else {
            this.$notify({
                group: 'notification',
                type: 'danger',
                title: 'Oops...',
                text: response.error
            });
        }
        this.$store.state.showLoading = false
    },
    async fetchMediaSocial() {
        var body = {
            "identifier" : "GET Media Social",
            "limit": 9999
        }
        var response = await apiRequest.requestRead(body)
        if(response.data != null){
            this.mediaSocial = response.data[0]
        }
    },
    goToDetail(id) {
        location.href = '/#/product/'+id
    },
    goToShop() {
        location.href = '/#/shop/'+this.query
    },
	closeSidebar () {
    //   if (this.activeSidebar.length > 0) {
    //     var key = this.activeSidebar.pop()
    //     this.$store.state.sidebar[key] = false
    //   }

        for (var key in this.$store.state.sidebar) {
            this.$store.state.sidebar[key] = false
        }
    },
    resize() {
    //  this.paymentHeight = helper.resizeElement('paymentImage', 1)
    },
    checkNavType() {
        window.onbeforeunload = function() {
            if(this.$route.path == '/cart/checkout') {
                    return true
            }
            else {
                    return null
            }
        }
        // if(this.$route.path == '/cart/checkout') {
        //     window.onbeforeunload = function() {
        //         return true
        //     }
        // }
        // else {
        //     window.onbeforeunload = function() {
        //         return null
        //     }
        // }
    },
    async logout () {
      var response = await apiRequest.logout()
        if(response.data != null){
            location.reload()
        }
        else {
            this.$notify({
                group: 'notification',
                type: 'danger',
                title: 'Oops...',
                text: response.error
            });
        }
    },
  }
}
</script>

