<template>
  <div class="container">
      <div class="col-12">
          <div class="row mb-5 mt-4">
            <div class="section_title col-12 px-0">
                <h2><span> <strong>{{"cart.myCart"|translate}}</strong></span></h2>
            </div>
            <div class="col-lg-8 col-sm-12 mb-3" v-if="dataCart.length != 0">
                <div class="row">
                    <div class="col-12 card">
                        <div class="row my-3" v-for="(data, index) in dataCart" :key="index">
                            <div class="text-right col-lg-1 col-md-1 col-sm-1 mb-3 align-self-center">
                                <p-check @change="updateCheckItem(index)" class="p-default p-round" :checked="data.is_selected == 1 ? true : false">
                                </p-check>
                            </div>
                            <div class="col-lg-2 col-md-3 col-sm-3 mb-3">
                                <img v-if="data.item_details.item.image" :src="$store.state.baseURL + encodeURIComponent(data.item_details.item.image)" width="100%" class="card"/>
                                <img v-else src="@/assets/noimage.jpg" width="100%" class="card"/>
                            </div>
                            <div class="col-lg-9 col-md-8 col-sm-8">
                                <div class="row">
                                    <div class="col-12">
                                            <h4><strong>{{data.item_details.item.item_name || ''}}</strong></h4>
                                            <div class="price_box" v-if="data.flash_sale_item.length == 0">
                                                <span class="current_price">{{ getCurrency(data.item_details.item_detail.price_list_rate) }}</span>
                                            </div>
                                            <div class="price_box" v-else>
                                                <span class="old_price">{{ getCurrency(data.item_details.item_detail.price_list_rate) }}</span><br/>
                                                <span class="current_price">{{ getCurrency(calculateDiscount(data.flash_sale_item[0], data.item_details.item_detail.price_list_rate)) }}</span>
                                            </div>
                                            <stepper 
                                            v-debounce="updateCart" 
                                            :debounce-events="['click', 'keyup']"
                                            class="stepper mt-2" 
                                            @model="data.qty = $event" :minimum="minQty[index] || 1" 
                                            :current="data.qty" 
                                            :maximum="data.item_details.item_detail.actual_qty || 1"
                                            :multiplication="multiplicationQty[index] || 1"/>
                                            <p>/ {{data.uom}}</p>
                                    </div>
                                </div>
                            </div>
                            <div class="col-12 text-right" style="font-size: 20px;">
                                <!-- <span class="fa fa-heart-o ishover"></span> -->
                                <span @click="confirmRemoveFromCart(index)" class="lnr lnr-trash ml-3 ishover"></span>
                            </div>
                            <div class="col-12 text-right mt-2">
                                <div :style="index < dataCart.length - 1 ? 'display: block' : 'display: none'" class="line"></div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="col-lg-4 col-sm-12 mb-3" v-if="dataCart.length != 0">
                <div class="sticky-right">
                    <div class="row card pt-3">
                        <div class="col-12">
                            <h4>{{"cart.summary"|translate}}</h4>
                            <div class="line my-3"></div>
                            <div class="row">
                                <div class="col-7">
                                    <h4>{{"cart.total"|translate}}</h4>
                                </div>
                                <div class="col-5 text-right">
                                    <h4 class="price-color">{{ countTotal() }}</h4>
                                </div>
                            </div>
                            <div class="mt-2" :class="isLoadingUpdateCart ? 'cart_links_btn_disabled' : 'cart_links_btn'">
                                <router-link to="/cart/checkout"><b>{{"cart.checkout"|translate}}</b></router-link>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            
            <!-- NODATA STATE -->
            <div v-if="dataCart.length == 0" class="col-12">
                <no-data :image="'/img/nodata/cart.jpg'" :title="'noData.yourShoppingCartIsEmpty'" :subtitle="'noData.letsFillInYourFavouriteItems'" />
            </div>
        </div>
      </div>
      <confirmation-modal name="removeCartModal">
        <template v-slot:title>
            <div class="text-center">
                <h4 style="font-size: 20px">{{ "cart.deleteItem" | translate }}</h4>
            </div>
        </template>

        <template v-slot:subtitle>
            <div class="text-center" v-if="dataCart.length > 0 && selectedDeletedItem > -1">
                <div class="mb-2">
                    <img v-if="dataCart[selectedDeletedItem].item_details.item.image" :src="$store.state.baseURL + encodeURIComponent(dataCart[selectedDeletedItem].item_details.item.image)" width="25%" class="is-circle"/>
                    <img v-else src="@/assets/noimage.jpg" width="25%" class="is-circle"/>
                </div>
                <div>
                    <h5 class="mb-0">{{ dataCart[selectedDeletedItem].item_details.item.item_name }}</h5>
                    <p>{{ 'cart.fromCart?' | translate }}</p>
                </div>
            </div>
        </template>

        <template v-slot:action>
            <div class="row mx-0 mt-2 mb-3">
                <div class="col-6 pr-1 text-center">
                    <!-- <div class="btn-modal py-2" @click="$modal.hide('removeCartModal')">Tidak</div> -->
                    <button class="btn br-8 btn-block btn-sm btn-outline-danger" @click="$modal.hide('removeCartModal')"><b>{{ 'Tidak' }}</b></button>
                </div>
                <div class="col-6 pl-1 text-center">
                    <!-- <div class="btn-block btn-modal-positive py-2">Ya</div> -->
                    <button class="btn br-8 btn-block btn-sm btn-danger" @click="deleteFromCart"><b>{{ 'Ya' }}</b></button>
                </div>
            </div>
        </template>
        
        <template v-slot:background>
            <img src="/img/modal/remove_cart.jpg" style="opacity: 0.05; position: absolute; bottom: 0; right: 0; width: 100%; height: 100%; object-fit: cover"/>
        </template>
      </confirmation-modal>
  </div>
</template>

<style>
.modal-cart {
    border-radius: 8px !important;
}
.br-8 {
    border-radius: 8px !important;
}
.mw-200 {
    max-width: 200px !important;
}
</style>

<script>
import apiRequest from '@/helper/apiRequest.js'
import currency from '@/helper/currency.js'
import helper from '@/helper/global.js'
// import Vue from 'vue'

export default {
  name: 'Blog',
  components: { 
      
    },
  data () {
    return {
      dataCart: [],
      minQty: [],
      multiplicationQty: [],
      isLoadingUpdateCart: false,

      selectedDeletedItem: -1
    }
  },
  created () {
    //   Vue.loadScript('/js/test.js');
    this.$store.state.showLoading = false
    if (apiRequest.isGuest()){
        location.href = '/#/auth'
    }
    else {
        this.fetchData()
    }
  },
  methods: {
    calculateDiscount (discount, amount) {
        return helper.calculateDiscount(discount, amount)
    },
    confirmRemoveFromCart (index) {
        this.selectedDeletedItem = index
        console.log(this.dataCart[index])
        this.$modal.show('removeCartModal')
    },
    getCurrency(money) {
        return currency.format('IDR', money)
    },
    countTotal() {
        var ttl = 0.0
        for (let i in this.dataCart){
            if (this.dataCart[i].is_selected == 1) {
                if (this.dataCart[i].flash_sale_item.length == 0) {
                    ttl += this.dataCart[i].item_details.item_detail.price_list_rate * parseInt(this.dataCart[i].qty)
                } else {
                    ttl += this.calculateDiscount(this.dataCart[i].flash_sale_item[0], this.dataCart[i].item_details.item_detail.price_list_rate) * parseInt(this.dataCart[i].qty)
                }
            }
        }
        return this.getCurrency(ttl)
    },
    async fetchData() {
        this.$store.state.showLoading = true
        var body = {
            "identifier" : "Get Shopping Cart",
            "limit": 9999
        }
        var response = await apiRequest.requestRead(body)
        if(response.data != null){
            console.log('cart',response.data)
            this.dataCart = []
            this.minQty = []
            this.multiplicationQty = []
            for (var index in response.data) {
                this.dataCart.push({...response.data[index]})
                var item = response.data[index]
                var uoms = item.item_details.item.uoms
                var me = this
                uoms.forEach(function (value) {
                    if (value.uom == item.uom) {
                        me.minQty.push(value.minimal_qty)
                        me.multiplicationQty.push(value.multiplication_qty)
                    }
                })
            }
            console.log('minqty',this.minQty)
            // console.log(`Data Cart ${JSON.stringify(this.dataCart)}`)
        }
        else {
            this.$notify({
                group: 'notification',
                type: 'danger',
                title: 'Oops...',
                text: response.error
            });
        }
        this.$store.state.showLoading = false
    },
    async updateCart() {
        this.isLoadingUpdateCart = true
        var cartData = []
        for(let idx in this.dataCart) {
            var x = {}
            Object.assign(x, this.dataCart[idx])
            delete x['flash_sale']
            delete x['flash_sale_item']
            // delete x['item_details']
            cartData.push(x)
        }
        var body = {
            "doctype" : "Shopping Cart",
            "name": this.$store.state.email,
            "shopping_cart_item": cartData
        }
        console.log("Update Cart",JSON.stringify(body))
        var response = await apiRequest.createOrUpdateDoc(body)
        console.log("UPDATE CART",response)
        if(response.error != null){
            this.$notify({
                group: 'notification',
                type: 'danger',
                title: 'Oops...',
                text: response.error
            });
        }
        this.isLoadingUpdateCart = false
        apiRequest.getCartBadge()
    },
    updateCheckItem(idx) {
        if (this.dataCart[idx].is_selected == 0) this.dataCart[idx].is_selected = 1
        else this.dataCart[idx].is_selected = 0
        this.updateCart()
        console.log("UPDATE CHECKED ITEM", this.dataCart[idx])
    },
    deleteFromCart () {
        this.$modal.hide('removeCartModal')
        if (this.selectedDeletedItem >= 0) {
            this.dataCart.splice(this.selectedDeletedItem,1)
            this.updateCart()
        }
    }
  }
}
</script>
