<template>
    <!--Side bar Choose Delivery-->
    <div :class="$store.state.sidebar.chooseDelivery ? 'active' : ''" class="mini_cart">
        <div class="cart_close mb-1">
            <div class="cart_text">
                <h3>Choose Delivery</h3>
            </div>
            <div class="mini_cart_close" @click="$store.state.sidebar.chooseDelivery = false">
                <a href="javascript:void(0)"><i class="ion-android-close"></i></a>
            </div>
        </div>
        <div class="mb-5">
            <div class="cart_item row align-items-center my-1 mx-1 ishover" v-for="(obj, index) in $store.state.listDeliveryService" :key="'delivery_' + index" @click="chooseDelivery(obj, index)">
                <div class="col-3 ">
                    <!-- Image -->
                    <img id="deliveryImage" :src="$store.state.baseURL + obj.image" class="card" alt="logo" width="85%">
                </div>
                <div class="col-7">
                    <!-- Title / Text -->
                    {{ obj.title }}
                </div>
                <div class="col-2">
                    <!-- Radio button -->
                    <p-check class="p-tada p-locked p-plain p-icon p-round p-bigger" :checked="deliveryDataSelected === index">
                        <i class="icon lnr lnr-checkmark-circle" slot="extra"></i>
                    </p-check>
                </div>
            </div>
        </div>

        <transition name="slide">
            <div v-show="deliveryDataSelected !== -1">
                <div class="cart_close" >
                    <div class="cart_text">
                        <h3>Choose Service</h3>
                    </div>
                </div>
                <div>
                    <div class="cart_item row align-items-center my-1 mx-1 ishover" v-for="(obj, index) in deliveryService" :key="'delivery_' + index" @click="chooseDeliveryService(index)">
                        <div class="col-10">
                            <!-- Title / Text -->
                            {{ obj.type }}<br>
                            {{ getCurrency(obj.price) }}
                        </div>
                        <div class="col-2">
                            <!-- Radio button -->
                            <p-check class="p-tada p-locked p-plain p-icon p-round p-bigger" :checked="deliveryServiceSelected === index">
                                <i class="icon lnr lnr-checkmark-circle" slot="extra"></i>
                            </p-check>
                        </div>
                    </div>
                </div>
            </div>
        </transition>
        <transition name="slide">
            <div class="cart_links_btn mt-4" v-show="deliveryDataSelected != -1 && deliveryServiceSelected != -1" @click="applyDeliveryCourier()">
                <a>Apply</a>
            </div>
        </transition>
    </div>
    <!--Sidebar Choose Delivery end-->
</template>

<script>
import helper from '@/helper/global.js'
import currency from '@/helper/currency.js'
import apiRequest from '@/helper/apiRequest.js'

export default {
    data () {
        return {
            deliveryDataSelected: -1,
            deliveryServiceSelected: -1,
            deliveryService: []
        }
    },
    created () {
        window.addEventListener('resize', this.resize)

    },
    watch: {
        '$store.state.sidebar.chooseDelivery': function (newVal) {
            if (newVal) {
                this.deliveryDataSelected = -1
                this.deliveryServiceSelecte = -1
            }
        }
    },
    mounted () {
        this.resize()
    },
    methods: {
        async applyDeliveryCourier() {
            this.$store.state.showLoading = true
            this.$store.state.sidebar.chooseDelivery = false
            var body = {
                "name" : this.$store.state.shoppingInvId,
                "doctype": "Shopping Cart Invoice",
                "courier": this.$store.state.listDeliveryService[this.deliveryDataSelected].name,
                "service": this.$store.state.listDeliveryService[this.deliveryDataSelected].service[this.deliveryServiceSelected].service_code,
                "eta": this.$store.state.listDeliveryService[this.deliveryDataSelected].service[this.deliveryServiceSelected].etd_from + " - " + this.$store.state.listDeliveryService[this.deliveryDataSelected].service[this.deliveryServiceSelected].etd_thru + " days"
            }
            var response = await apiRequest.createOrUpdateDoc(body)
            if(response.data != null){
                this.$store.state.selectedDeliveryService = {
                    "name": this.$store.state.listDeliveryService[this.deliveryDataSelected].name,
                    "title": this.$store.state.listDeliveryService[this.deliveryDataSelected].title,
                    "image": this.$store.state.listDeliveryService[this.deliveryDataSelected].image,
                    "service": this.$store.state.listDeliveryService[this.deliveryDataSelected].service[this.deliveryServiceSelected]
                }
                this.deliveryDataSelected = -1,
                this.deliveryServiceSelected = -1,
                this.deliveryService = []
            }
            else {
                this.$notify({
                    group: 'notification',
                    type: 'danger',
                    title: 'Oops...',
                    text: response.error
                });
            }
            this.$store.state.showLoading = false
        },
        resize() {
            this.imageHeight = helper.resizeElement('deliveryImage', 1)
        },
        chooseDelivery (obj, index) {
            this.deliveryDataSelected = index
            this.deliveryServiceSelected = -1
            this.deliveryService = obj.service
        },
        chooseDeliveryService (index) {
            this.deliveryServiceSelected = index
        },
        getCurrency(money) {
            return currency.format('IDR', parseFloat(money))
        },
    }
}
</script>