<template>
    <div class="text-center">
        <div @click="$emit('click')" class="variant-box relative" :class="active ? 'variant_active' : 'variant_non_active'" style="padding:6px !important">
            <div id="variantTitle" class="variant_title" style="word-break: break-all;margin:0 !important;margin-top:4px;padding:4 !important" >{{ title }}</div>
        </div>
        
    </div>
</template>

<script>
import helper from '@/helper/global.js'

export default {
  props: [
    'active',
    'width',
    'title'
  ],
  data () {
    return {
      titleHeight: 0,
      titleMargin: 10
    }
  },
  computed: {
    titleStyle () {
      var style = this.titleHeight > 0 ? ('margin-top: -' + this.titleHeight/2 + 'px; ' ) : ''

      style += this.width > 0 ? ('max-width: ' + (this.width - this.titleMargin*2)  + 'px; ') : ''

      return style
    }
  },
  created () {
    window.addEventListener('resize', this.resize)
  },
  mounted () {
    this.resize()
  },
  methods: {
    resize () {
      this.titleHeight = helper.getElementHeight('variantTitle')
    },
  }
}
</script>