import Vue from 'vue'
import Vuex from 'vuex'
import vuexI18n from 'vuex-i18n'
import translationsEn from './locale/en.json'
import translationsId from './locale/id.json'

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        isMobile: false,
        guestToken: 'token 8dbc66f2f3a71d8:47f3018afd99a48',
        // guestToken: 'token 2beeacebe0bf438:fefbdebae37f5d3',
        // baseURL: '/api/call.php?url=',
        baseURL: '',
        defaultCustomer: 'API WSS',
        showLoading: false,
        locale: 'en',
        fullname: '',
        email: '',
        
        // Sidebar
        sidebar: {
            chooseDelivery: false,
            chooseAddress: false,
            addAddress: false,
            salesInvoice: false,
            payment: false
        },
        sidebarCallback: () => {},
        sidebarData: null,
        sidebarEvents: null,
        invoice: {},
        
        // CHECKOUT
        selectedAddress: null,
        listAddress: [],
        selectedDeliveryService: null,
        listDeliveryService: [],
        shoppingInvId: '',
        listPayment: [],
        selectedPayment: null,

        //NAV BAR
        wishlist: 0,
        cart: 0,

        //Browse Category
        browseCategory: ''
        
    },
    mutations: {
        setCulture(state, payload) {
          state.locale = payload
          localStorage.setItem('locale', payload)
          Vue.i18n.set(state.locale);
        },
        setSidebar(state, payload) {
          state.sidebar[payload.sidebar] = payload.open
          
          if (payload.data) {
            state.sidebarData = payload.data
          } else {
            state.sidebarData = null
          }

          if (payload.events) {
            state.sidebarEvents = payload.events
          } else {
            state.sidebarEvents = null
          }

          if (payload.callback) {
            state.sidebarCallback = payload.callback
          } else {
            state.sidebarCallback = () => {}
          }
        }
    },
    getters: {
        language(state) {
          return state.locale
        }
    },
    actions: {
      changeCulture({ commit }, payload) {
        console.log("CHANGE LANG", payload)
        this.state.locale = payload
        commit('setCulture', payload)
      },
      openSideBar({ commit }, payload) {
        commit('setSidebar', payload)
      }
    }
})


const store = new Vuex.Store()
Vue.use(vuexI18n.plugin,store)

Vue.i18n.add('en', translationsEn)
Vue.i18n.add('id', translationsId)

Vue.i18n.set('en')