<template>
    <div>
        <div class="container my-5 px-5">
            <div class="text-center">
                <h3>{{ "footer.help.termAndCondition" | translate }}</h3>
                <p class="text-justify mt-4" v-html="data.term_and_condition">
                    
                </p>
            </div>
        </div>
    </div>
</template>

<script>
import apiRequest from '@/helper/apiRequest.js'

export default {
    created () {
        this.$store.state.showLoading = false
        this.fetchData()
    },
    data() {
        return {
            data: {}
        }
    },
    methods: {
        async fetchData () {
            this.$store.state.showLoading = true
            var body = {
                'identifier': 'GET Company Term and Policy'
            }
            var response = await apiRequest.requestRead(body)
            if(response.data != null){
                if (response.data.length > 0) {
                    this.data = response.data[0]
                }
            }
            else {
                this.$notify({
                    group: 'notification',
                    type: 'danger',
                    title: 'Oops...',
                    text: response.error
                });
            }
            this.$store.state.showLoading = false
        },
    }
}
</script>