<template>
    <div>
        <!--shop area start-->
        <div class="shop_area">
            <div class="container">
                <div class="row">
                    <div class="col-lg-3 col-md-12">
                        <!--sidebar widget start-->
                        <aside class="sidebar_widget">
                            <div class="widget_inner">
                                <div class="widget_list widget_categories">
                                    <h2>{{"shop.categories" | translate}}</h2>
                                    <ul>
                                        <li v-for="(data,index) in itemCategory" :key="'item'+index" @click="data.isSelected = !data.isSelected; updateFilter()" >
                                            <input type="checkbox" :checked="data.isSelected">
                                            <a>{{ data.item_group }}</a>
                                            <span class="checkmark"></span>
                                        </li>
                                    </ul>
                                </div>
                                <div class="widget_list widget_categories">
                                    <h2>{{"shop.brand" | translate}}</h2>
                                    <ul>
                                        <li v-for="(data,index) in dataBrand" :key="index" @click="data.isSelected = !data.isSelected; updateFilter()">
                                            <input type="checkbox" :checked="data.isSelected">
                                            <a>{{ data.brand }}</a>
                                            <span class="checkmark"></span>
                                        </li>
                                    </ul>
                                </div>
                                <div class="widget_list widget_categories">
                                    <h2>{{"shop.price" | translate}}</h2>
                                    <form class="text-center">
                                        <vue-range-slider
                                            @drag-end="updateFilter(false)"
                                            ref="slider"
                                            :min="filterPrice.min"
                                            :max="filterPrice.max"
                                            v-model="filterPrice.value"
                                            :step="filterPrice.step"
                                            :formatter="filterPrice.format"
                                            :process-style="filterPrice.processStyle"
                                            :tooltip-style="filterPrice.tooltipStyle"
                                            class="mb-2">
                                        </vue-range-slider>
                                        <p type="text" name="text" class="mb-2" id="amount">{{rangeFilterPrice}}</p>
                                    </form>
                                </div>
                            </div>
                        </aside>
                        <!--sidebar widget end-->
                    </div>
                    <div class="col-lg-9 col-md-12">
                        <!--shop wrapper start-->
                        <!--shop toolbar start-->
                        <!-- <div class="shop_title">
                            <h1>shop</h1>
                        </div> -->
                        <div class="row">
                            <div class="col-12">
                                <div class="section_title">
                                    <h2><span> <strong>{{"shop.shop" | translate}}</strong></span></h2>
                                    <ul class="product_tab_button nav" role="tablist">
                                        <div class=" niceselect_option mb-0">
                                            <form class="select_option" action="#">
                                                <select name="orderby" id="sortShop" @change="onSortChange($event)">
                                                    <option selected value="ti.modified DESC">{{ 'shop.all' | translate }}</option>
                                                    <option value="tip.price_list_rate ASC">{{ 'shop.priceLowToHigh' | translate }}</option>
                                                    <option value="tip.price_list_rate DESC">{{ 'shop.priceHighToLow' | translate }}</option>
                                                    <option value="ti.creation DESC">{{ 'shop.latest' | translate }}</option>
                                                    <option value="ti.item_name ASC">{{ 'shop.sortAZ' | translate }}</option>
                                                    <option value="ti.item_name DESC">{{ 'shop.sortZA' | translate }}</option>
                                                </select>
                                            </form>
                                        </div>
                                    </ul>
                                </div>
                            </div>
                        </div>
                        <!-- <div class="shop_toolbar_wrapper mx-1">
                            <div class=" niceselect_option">
                                <form class="select_option" action="#">
                                    <select name="orderby" id="short">
                                        <option selected value="1">All</option>
                                        <option value="2">Sort by price: low to high</option>
                                        <option value="3">Sort by price: high to low</option>
                                        <option value="4">Sort by Newness</option>
                                        <option value="4">Sort by Product Name: A-Z</option>
                                        <option value="4">Sort by Product Name: Z-A</option>
                                    </select>
                                </form>
                            </div>
                            <div class="page_amount">
                                <p>Showing 19 of 21 results</p>
                            </div>
                        </div> -->
                        <!--shop toolbar end-->
                        <div v-if="$route.params.search" class="mb-2">
                            <p>{{"shop.searchResultFor"|translate}} "{{$route.params.search}}"</p>
                        </div>
                        <div class="row shop_wrapper grid_4 mx-0">
                            <div
                                class="col-md-4 col-sm-6 col-lg-3 p-1"
                                v-for="(data,index) in dataProduct"
                                :key="index">
                                <!-- {{data.name}} -->
                                <single-product-tokopedia
                                        :data="{ 
                                            'title': data.item_name,
                                            'category': data.item_group,
                                            'old_price': data.flash_sale_item == null ? 0 :(data.flash_sale_item.length > 0 ? getCurrency(data.price_list_rate) : 0),
                                            'rating': 0,
                                            'discount': 0,
                                            'flash_sale': data.flash_sale_item == null ? '' :(data.flash_sale_item.length > 0 ? data.flash_sale_item[0].title : ''),
                                            'image': data.image,
                                            'current_price': getCurrency(data.flash_sale_item == null ? data.price_list_rate :(data.flash_sale_item.length > 0 ? calculateDiscount(data.flash_sale_item[0], data.price_list_rate) : data.item_price)),
                                            'id': data.item_code,
                                            'is_liked': data.is_liked
                                        }">
                                </single-product-tokopedia>
                            </div>
                        </div>

                        <div v-if="isLoadingItem">
                            <loading style="margin-left: auto; margin-right: auto" :animation-duration="1000" :dot-size="15" :dots-num="3" :color="'#e74c34'"/>
                        </div>

                        <div class="cart_links_btn mt-4" v-if="!isLoadingItem && !isLastPage" @click="loadMore()">
                            <a>{{"global.loadMore" | translate}}</a>
                        </div>

                        <!-- NODATA STATE -->
                        <no-data v-if="isProductNotFound" :image="'/img/nodata/product.jpg'" :title="'noData.oopsProductNotFound'" :subtitle="'noData.pleaseTryAnotherKeyword'" />
                        
                    </div>
                </div>
            </div>
        </div>
        <!--shop  area end-->
    </div>
</template>

<script>
import apiRequest from '@/helper/apiRequest.js'
import currency from '@/helper/currency.js'
import 'vue-range-component/dist/vue-range-slider.css'
import VueRangeSlider from '@/component/vue-slider/new-vue-slider.js'
import helper from '@/helper/global.js'

export default {
  name: 'Shop',
  data () {
    return {
      dataProduct: [],
      dataBrand: [],
      page : 0,
      isLoadingItem: true,
      isLastPage: false,
      itemCategory: [],
      filterPrice: {
        step: 500,
        min: 0,
        max: 999999999,
        value: [0, 999999999],
        format: (value => `Rp${value},00`),

        processStyle: {
            backgroundColor: '#e74c34'
        },
        tooltipStyle: {
            display: 'none'
        }
      },
      selectedSort : 'ti.modified DESC',
      isProductNotFound: false
    }
  },
  components: {
    'vue-range-slider': VueRangeSlider,
  },
  computed: {
    rangeFilterPrice () {
      return this.getCurrency(this.filterPrice.value[0]) + ' - ' + this.getCurrency(this.filterPrice.value[1]) 
    }
  },
  created () {
    this.fetchItemCategory()
  },
  methods: {
    calculateDiscount (discount, amount) {
        return helper.calculateDiscount(discount, amount)
    },
    async fetchItem(page) {
        this.isLoadingItem = true
        this.isProductNotFound = false
        // FETCH ITEMS
        // var body = {
        //     "identifier" : "Get Shop Website",
        //     "brand" : await this.getBrand(),
        //     "item_group" : await this.getItemGroup(),
        //     "max_price" : this.filterPrice.value[1],
        //     "min_price" : this.filterPrice.value[0],
        //     "page" : page,
        //     "limit" : 20,
        //     "search": this.$route.params.search ? this.$route.params.search : '',
        //     "order_by": this.selectedSort
        // }
        var  body = {
                "search" : this.$route.params.search ? this.$route.params.search : '',
                "page" : page,
                "flash_sale" : "%",
                "item_group" : await this.getItemGroup(),
                "brand" : await this.getBrand(),
                "popular" : "%",
                "new_arrival" : "%",
                "min_price" : this.filterPrice.value[0],
                "max_price" : this.filterPrice.value[1],
                "limit" : 20,
                "order_by": this.selectedSort
            }
        console.log('Body Get Item',body)
        var response = await apiRequest.getItems(body)
        console.log('Get Item',response.data)
        if(response.data != null){
            if (page == 0) {
                this.dataProduct = []
                if (response.data.length == 0){
                    this.isProductNotFound = true
                }
            }
            for (let i in response.data){ 
                var data = response.data[i]
                this.dataProduct.push(data)
            }
        }
        else {
            this.$notify({
                group: 'notification',
                type: 'danger',
                title: 'Oops...',
                text: response.error
            });
        }
        if (response.data.length == 0) {
            this.isLastPage = true
        }
        if (response.data.length < 20) {
            this.isLastPage = true
        }
        this.isLoadingItem = false
    },
    async fetchBrand() {
        this.$store.state.showLoading = true
        var body = {
            "identifier" : "Get Brand",
            "page" : 0,
            "limit" : 999
        }
        var response = await apiRequest.requestRead(body)
        console.log('Get Brand',response.data)
        if(response.data != null){
            this.dataBrand = []
            for (let i in response.data){
                var data = {
                    "name": response.data[i].name,
                    "brand": response.data[i].brand,
                    "isSelected": false,
                }
                this.dataBrand.push(data)
            }
        }
        else {
            this.$notify({
                group: 'notification',
                type: 'danger',
                title: 'Oops...',
                text: response.error
            });
        }
        this.updateFilter()
        this.$store.state.showLoading = false
    },
    async fetchItemCategory() {
        var body = {
            "identifier" : "Get Item Category",
            "limit": 9999
        }
        var response = await apiRequest.requestRead(body)
        if(response.data != null){
            this.itemCategory = []
            for (let i in response.data){
                var data = {
                    "item_group": response.data[i].item_group,
                    "isSelected": false,
                }
                this.itemCategory.push(data)
            }
        }
        else {
            this.$notify({
                group: 'notification',
                type: 'danger',
                title: 'Oops...',
                text: response.error
            });
        }
        
        this.checkCategory()
        this.fetchBrand()
    },
    checkCategory () {
        if (localStorage.getItem('browseCategory')) {
            this.itemCategory.forEach(el => {
                if (el.item_group == localStorage.getItem('browseCategory')) {
                    el.isSelected = true
                }
            })

            localStorage.removeItem('browseCategory')
        }
    },
    getCurrency(money) {
        return currency.format('IDR', money)
    },
    loadMore(){
        this.page += 20
        this.fetchItem(this.page)
    },
    async fetchMinMax() {
        // FETCH MINMAX
        // var bodyMinMax = {
        //     "identifier" : "Get MIN MAX Filter Shop Website",
        //     "brand" : await this.getBrand(),
        //     "item_group" : await this.getItemGroup(),
        //     "search": this.$route.params.search ? this.$route.params.search : ''
        // }
        var bodyMinMax = {
                "search" : this.$route.params.search ? this.$route.params.search : '',
                "flash_sale" : "%",
                "item_group" : await this.getItemGroup(),
                "brand" : await this.getBrand(),
                "popular" : "%",
                "new_arrival" : "%",
                "order_by": this.selectedSort
        }
        var responseMixMax = await apiRequest.getMinMaxItems(bodyMinMax)
        console.log('Get Min Max',responseMixMax.data)
        if(responseMixMax.data != null){
            // this.filterPrice.min = responseMixMax.data[0].min || 0
            // this.filterPrice.max = responseMixMax.data[0].max || 0
            // this.filterPrice.value = [responseMixMax.data[0].min || 0, responseMixMax.data[0].max || 0]
            this.filterPrice.min = responseMixMax.data.min_price || 0
            this.filterPrice.max = responseMixMax.data.max_price || 0
            this.filterPrice.value = [responseMixMax.data.min_price || 0, responseMixMax.data.max_price || 0]
            this.fetchItem(this.page)
        }
        else {
            this.$notify({
                group: 'notification',
                type: 'danger',
                title: 'Oops...',
                text: responseMixMax.error
            });
        }
    },
    async getItemGroup() {
        // CHECK ITEM GROUP
        var list_item_group = []
        var list_item_group_all = []
        var item_group = ""
        var has_checked_ig = false
        for (let idx in this.itemCategory) {
            if (this.itemCategory[idx].isSelected){
                list_item_group.push('"'+ this.itemCategory[idx].item_group +'"')
                has_checked_ig = true
            }
            list_item_group_all.push('"'+ this.itemCategory[idx].item_group +'"')
        }
        if (has_checked_ig) {
            for(let idx in list_item_group){
                if(idx == 0){
                    item_group += list_item_group[idx]
                }
                else {
                    item_group += ","+list_item_group[idx]
                }
            }
        }
        else {
            for(let idx in list_item_group_all){
                if(idx == 0){
                    item_group += list_item_group_all[idx]
                }
                else {
                    item_group += ","+list_item_group_all[idx]
                }
            }
        }
        console.log("ITEM GROUP", item_group)

        var arrayIG = item_group.replace(/"/g,"").split(",")
        return arrayIG
    },
    async getBrand() {
        // CHECK BARND
        var list_brand = []
        var list_brand_all = []
        var brand = ""
        var has_checked_brand = false
        for (let idx in this.dataBrand) {
            if (this.dataBrand[idx].isSelected){
                list_brand.push('"'+ this.dataBrand[idx].name +'"')
                has_checked_brand = true
            }
            list_brand_all.push('"'+ this.dataBrand[idx].name +'"')
        }
        if (has_checked_brand) {
            for(let idx in list_brand){
                if(idx == 0){
                    brand += list_brand[idx]
                }
                else {
                    brand += ","+list_brand[idx]
                }
            }
        }
        else {
            for(let idx in list_brand_all){
                if(idx == 0){
                    brand += list_brand_all[idx]
                }
                else {
                    brand += ","+list_brand_all[idx]
                }
            }
        }
        console.log("BRAND", brand)
        // return brand
        var arrayBrand = brand.replace(/"/g,"").split(",")
        return arrayBrand
    },
    async updateFilter(fetchMinMax = true){
        this.page = 0
        this.dataProduct = []
        if (fetchMinMax){
            this.fetchMinMax()
        }
        else {
            this.fetchItem(this.page)
        }
    },
    onSortChange(event) {
        this.selectedSort = event.target.value
        console.log(event.target.value)
        this.updateFilter(false)
    }
  }
}
</script>
