var currency = {}

currency.format = (country_code, money) => {
    // console.log(country_code, money)
    let val = ""
    let abbr = "Rp."
    if(money == 0){
        return localStorage.getItem('locale') == 'en' ? 'Free' : 'Gratis';
    }
    switch (country_code) {
        case 'USD':
            val = (money).toFixed(2).replace(/\d(?=(\d{3})+\.)/g, '$&,')
            abbr = '$'
            break
        case 'IDR':
            val = (money).toFixed(2).replace(/\d(?=(\d{3})+\.)/g, '$&,')
            val = val.replace(".00","")
            val = val.replace(/,/g,".")
            abbr = 'Rp.'
            break
        default:
            val = (money).toFixed(2).replace(/\d(?=(\d{3})+\.)/g, '$&,')
            val = val.replace(".00","")
            val = val.replace(/,/g,".")
            abbr = 'Rp.'
    }

    return abbr + ' ' + val;
}

export default currency