<template>
  <div>
    <!-- customer login start -->
    <div class="customer_login mt-32">
        <div class="container">
            <div class="row">
                <!--reset password start-->
                <div class="col-lg-6 col-md-6 col-12">
                    <div class="account_form sticky-left">
                        <h2>Reset Password</h2>
                        <form>
                            <p>
                                <label>Email <span>*</span></label>
                                <input v-model="reset.email" type="email">
                            </p>
                            <p>
                                <label>New Passwords <span>*</span></label>
                                <input id="login-password" v-model="reset.password" type="password">
                            </p>
                            <p>
                                <label>Confirm New Passwords <span>*</span></label>
                                <input id="login-password" v-model="reset.confirm" type="password" @keyup.enter="resetPwd">
                            </p>
                            <div class="login_submit">
                                <div class="auth_button" @click="resetPwd">Submit</div>
                            </div>
                        </form>
                    </div>
                </div>
                <!--reset password start-->
            </div>
        </div>
    </div>
    <!-- customer login end -->
  </div>
</template>

<script>
import apiRequest from '@/helper/apiRequest.js'

export default {
  name: 'Account',
  components: { 
      
   },
  data () {
    return {
        reset: {
            email: '',
            password: '',
            confirm: ''
        }
    }
  },
  created () {
        this.$store.state.showLoading = false
  },
  mounted(){
  },
  methods: {
    async resetPwd() {
        if (this.reset.email == "") {
            this.$notify({
                group: 'notification',
                type: 'danger',
                title: 'Oops...',
                text: 'Silahkan isi email'
            });
            return
        }
        else if (this.reset.password == "") {
            this.$notify({
                group: 'notification',
                type: 'danger',
                title: 'Oops...',
                text: 'Silahkan isi password'
            });
            return
        }
        else if (this.reset.confirm == "") {
            this.$notify({
                group: 'notification',
                type: 'danger',
                title: 'Oops...',
                text: 'Silahkan isi konfirmasi password'
            });
            return
        }
        else if (this.reset.confirm != this.reset.password) {
            this.$notify({
                group: 'notification',
                type: 'danger',
                title: 'Oops...',
                text: 'Konfirmasi password tidak sama'
            });
            return
        }
        this.$store.state.showLoading = true
        var body = {
            'user': this.reset.email,
            'password': this.reset.password,
            'key': this.$route.params.id,
        }
        var response = await apiRequest.resetPwd(body)
        if(response.data != null){
            this.$notify({
                group: 'notification',
                type: 'success',
                title: 'Yey...',
                text: 'Password berhasil di rubah.'
            });
            this.$router.push('/auth')
        }
        else {
            this.$notify({
                group: 'notification',
                type: 'danger',
                title: 'Oops...',
                text: response.error
            });
        }
        this.$store.state.showLoading = false
    },
  }
}
</script>
