import VueRangeSlider from 'vue-range-component'
import Vue from 'vue'

Vue.use(VueRangeSlider);
Vue.component('originalVueSlider', VueRangeSlider);

const newSlider = Vue.component('originalVueSlider').extend({
    methods: {
        handleKeydown(e) {
            e.stopPropagation()
            if (!this.useKeyboard) {
              return false
            }
            const keyCode = e.which || e.keyCode
            switch (keyCode) {
              case 37:
              case 40:
                this.keydownFlag = true
                this.flag = true
                this.changeFocusSlider(this.actionsKeyboard[0])
                break
              case 38:
              case 39:
                this.keydownFlag = true
                this.flag = true
                this.changeFocusSlider(this.actionsKeyboard[1])
                break
              default:
                break
            }
        },
    }
});
export default newSlider