<template>
    <div class="text-center">
        <div @click="$emit('click')" class="variant-box relative" :class="active ? 'variant_active' : 'variant_non_active'" :style="width === undefined ? 'width: 30px' : ('width: '+ width + 'px')">
            <!-- <img id="variantImage" src="@/assets/noimage.jpg" alt="variant" width="100%" :style="imageHeight > 0 ? ('height: ' + imageHeight + 'px') : ''"> -->
            <img  id="variantImage" :src="image != null && image != '' && image != undefined ? ($store.state.baseURL + encodeURIComponent(image)) : '@/assets/noimage.jpg'" alt="variant" width="100%" :style="imageHeight > 0 ? ('height: ' + imageHeight + 'px') : ''">
        </div>
        <div id="variantTitle" class="variant_title" style="word-break: break-all;" :style="titleStyle">{{ title }}</div>
    </div>
</template>

<script>
import helper from '@/helper/global.js'

export default {
  props: [
    'image',
    'active',
    'width',
    'title'
  ],
  data () {
    return {
      imageHeight: 0,
      titleHeight: 0,
      titleMargin: 10
    }
  },
  computed: {
    titleStyle () {
      var style = this.titleHeight > 0 ? ('margin-top: -' + this.titleHeight/2 + 'px; ' ) : ''

      style += this.width > 0 ? ('max-width: ' + (this.width - this.titleMargin*2)  + 'px; ') : ''

      return style
    }
  },
  created () {
    window.addEventListener('resize', this.resize)
  },
  mounted () {
    this.resize()
  },
  methods: {
    resize () {
      this.imageHeight = helper.resizeElement('variantImage', 1)
      this.titleHeight = helper.getElementHeight('variantTitle')
    },
  }
}
</script>