<script>
import carousel from 'vue-owl-carousel2'
import apiRequest from '@/helper/apiRequest.js'
import currency from '@/helper/currency.js'
import helper from '@/helper/global.js'

export default {
  name: 'Home',
  props: {
    msg: String
  },
  components: { carousel },
  data () {
    return {
      thumbnailCarouselKey: 0,
      productCarouselKey: 0,
      isOpenCategory: false,
      dataCarousel: [],
      dataBannerOurProduct: [],
      dataBannerLastView: [],
      dataBannerTopSeller: '',
      dataItemGroup: [],
      dataLastView: [],
      dataTopSeller: [],
      dataOurProduct: [],
      dataProductTab: ['Popular', 'New Arrival'],
      flashSaleData: null,
      ourProductSelected: 'Popular',
      hideCategory: true,
      limit: 20
    }
  },
  created () {
    this.fetchOurProduct("Popular")
      this.fetchCarousel()
    //   this.getFlashSaleItem()
      this.fetchTopSeller()
      this.fetchLastView()
  },
  methods: {
    moreCategory () {
        this.hideCategory = !this.hideCategory
    },
    getFlashSaleServerTime () {
        if (this.flashSaleData.now.length > 0) {
            return this.flashSaleData.now[0].now
        } else {
            return null
        }
    },
    navigate (carousel, method) {
        if (carousel in this.$refs) {
            console.log(this.$refs[carousel].owl.trigger(method + '.owl.carousel'))
        }
    },
    calculateDiscount (discount, amount) {
        return helper.calculateDiscount(discount, amount)
    },
    openCategories () {
      this.isOpenCategory = !this.isOpenCategory
    },
    async fetchCarousel() {
        this.$store.state.showLoading = true
        var body = {
            "identifier" : "Get Home Page"
        }
        var response = await apiRequest.requestRead(body,this.$store.state.guestToken)
        this.fetchItemGroup()
        if(response.data != null){
            if(response.data.length > 0){
                this.dataCarousel = response.data[0].carousel_banner_table
                if (this.$store.state.isMobile) {
                    this.dataBannerOurProduct = response.data[0].banner_our_product.slice(0, 1)
                } else {
                    this.dataBannerOurProduct = response.data[0].banner_our_product
                }
                this.dataBannerTopSeller = response.data[0].banner_top_seller
                this.dataBannerLastView = response.data[0].banner_last_view
                this.thumbnailCarouselKey += 1
            }
        }
        else {
            this.$notify({
                group: 'notification',
                type: 'danger',
                title: 'Oops...',
                text: response.error
            });
        }
        this.$store.state.showLoading = false
    },
    async fetchItemGroup() {
        this.$store.state.showLoading = true
        var body = {
            "identifier" : "Get Item Category With Children",
            "limit" : 99
        }
        var response = await apiRequest.requestRead(body)
        this.fetchOurProduct("Popular")
        if(response.data != null){
            if(response.data.length > 0){
                this.dataItemGroup = response.data
            }
        }
        else {
            this.$notify({
                group: 'notification',
                type: 'danger',
                title: 'Oops...',
                text: response.error
            });
        }
        this.$store.state.showLoading = false
    },
     openCategory (data) {
        this.isBrowseCategoriesOpened = false
        localStorage.setItem('browseCategory', data)
        
        if (this.$route.name == 'shop') {
            location.reload()
        } else {
            this.$router.push('/shop')
        }
    },
    async fetchOurProduct(selected) {
        this.ourProductSelected = selected
        this.$store.state.showLoading = true
        var body = {}
        if(this.ourProductSelected == "New Arrival"){
            body = {
                "search" : "",
                "page" : 0,
                "flash_sale" : "%",
                "item_group" : "",
                "popular" : "%",
                "new_arrival" : "1",
                "min_price" : "0",
                "max_price" : "1000000000",
                "limit" : this.limit
            }
        }
        else if(this.ourProductSelected == "Popular") {
            body = {
                "search" : "",
                "page" : 0,
                "flash_sale" : "%",
                "item_group" : "",
                "popular" : "1",
                "new_arrival" : "%",
                "min_price" : "0",
                "max_price" : "1000000000",
                "limit" : this.limit
            }
        }
        else {
            body = {
                "search" : "",
                "page" : 0,
                "flash_sale" : "%",
                "item_group" : "",
                "popular" : "1",
                "new_arrival" : "%",
                "min_price" : "0",
                "max_price" : "1000000000",
                "limit" : this.limit
            }
        }
        var response = await apiRequest.getItems(body)
        console.log('RESPONSE ITEM',response.data)
        if(response.data != null){
            this.dataOurProduct = []
            response.data.forEach(el => {
                this.dataOurProduct.push(el)
            })
            // this.dataOurProduct = response.data
            console.log("PRODUCT", this.dataOurProduct)
        }
        else {
            this.$notify({
                group: 'notification',
                type: 'danger',
                title: 'Oops...',
                text: response.error
            });
        }
        this.$store.state.showLoading = false
    },
    async fetchTopSeller() {
        var body = {
            "search" : "",
            "page" : 0,
            "flash_sale" : "%",
            "item_group" : "",
            "popular" : "%",
            "new_arrival" : "%",
            "min_price" : "0",
            "max_price" : "1000000000",
            "limit" : this.limit,
            "order_by": "ti.sold"
        }
        var response = await apiRequest.getItems(body)
        console.log('TOP SELLER',response.data)
        if(response.data != null){
            this.dataTopSeller = []
            this.dataTopSeller = response.data
            this.productCarouselKey += 1
        }
        else {
            this.$notify({
                group: 'notification',
                type: 'danger',
                title: 'Oops...',
                text: response.error
            });
        }
    },
    async fetchLastView() {
        var body = {
            "search" : "",
            "page" : 0,
            "flash_sale" : "%",
            "item_group" : "",
            "popular" : "%",
            "new_arrival" : "%",
            "min_price" : "0",
            "max_price" : "1000000000",
            "limit" : this.limit,
            "order_by": "ti.last_seen"
        }
        var response = await apiRequest.getItems(body)
        console.log('RESPONSE ITEM',response.data)
        if(response.data != null){
            this.dataLastView = []
            this.dataLastView = response.data
            this.productCarouselKey += 1
        }
        else {
            this.$notify({
                group: 'notification',
                type: 'danger',
                title: 'Oops...',
                text: response.error
            });
        }
    },
    async getFlashSaleItem() {
        var body = {
            "identifier" : "Get Flash Sale"
        }
        var response = await apiRequest.requestRead(body)
        console.log('RESPONSE ITEM',response.data)
        if(response.data != null){
            if(response.data.length > 0){
                this.flashSaleData = response.data[0]
            }
        }
        else {
            this.$notify({
                group: 'notification',
                type: 'danger',
                title: 'Oops...',
                text: response.error
            });
        }
    },
    getCurrency(money) {
        return currency.format('IDR', money)
    },
  }
}
</script>
