import Vue from 'vue'
import App from './App.vue'
import router from './router'
import axios from 'axios'

import store from './store'

// Helper
import apiRequest from '@/helper/apiRequest.js'
import helper from '@/helper/global.js'
import PrettyCheck from 'pretty-checkbox-vue/check';

Vue.component('p-check', PrettyCheck);
 
import PrettyRadio from 'pretty-checkbox-vue/radio';

Vue.component('p-radio', PrettyRadio);

Vue.use(apiRequest)
Vue.use(helper)

Vue.config.productionTip = false
import LoadScript from 'vue-plugin-load-script';
Vue.use(LoadScript);

import VueI18n from 'vue-i18n'
Vue.use(VueI18n)

import FlagIcon from 'vue-flag-icon'
Vue.use(FlagIcon);

// Register Local Component
import loading from './component/loading/loading.vue'
Vue.component('global-loading', loading)

import singleProductComponent from './component/home/singleProduct.vue'
Vue.component('single-product', singleProductComponent)

import singleProductTokopediaComponent from './component/home/singleProductTokopedia.vue'
Vue.component('single-product-tokopedia', singleProductTokopediaComponent)

import singleProductFlashSale from './component/home/singleProductFlashSale.vue'
Vue.component('single-product-flash-sale', singleProductFlashSale)

import stepper from './component/form/stepper.vue'
Vue.component('stepper', stepper)

import timer from './component/form/timer.vue'
Vue.component('timer', timer)

import timerD from './component/form/timerDetail.vue'
Vue.component('timer-detail', timerD)

import productVariant from './component/product-detail/variant.vue'
Vue.component('product-variant', productVariant)

import productUOM from './component/product-detail/uom.vue'
Vue.component('product-uom', productUOM) 

import sidebarDelivery from './component/sidebar/delivery.vue'
Vue.component('sidebar-delivery', sidebarDelivery)

import sidebarPayment from './component/sidebar/payment.vue'
Vue.component('sidebar-payment', sidebarPayment)

import sidebarAddressList from './component/sidebar/addressList.vue'
Vue.component('sidebar-address-list', sidebarAddressList)

import sidebarAddressForm from './component/sidebar/addressForm.vue'
Vue.component('sidebar-address-form', sidebarAddressForm)

import sidebarSalesInvoice from './component/sidebar/salesInvoice.vue'
Vue.component('sidebar-sales-invoice', sidebarSalesInvoice)

import VModal from 'vue-js-modal'
Vue.use(VModal)

import confirmationModal from './component/dialog/confirmation.vue'
Vue.component('confirmation-modal', confirmationModal)

import {HollowDotsSpinner} from 'epic-spinners'
Vue.component('loading',HollowDotsSpinner)

import { HalfCircleSpinner } from 'epic-spinners'
Vue.component('half-circle-loading',HalfCircleSpinner)

import VueHZoom from './component/vue-zoom/new-vue-zoom.js';
Vue.component('vue-zoom', VueHZoom)

import VueAutosuggest from 'vue-autosuggest'
Vue.use(VueAutosuggest)
Vue.component(VueAutosuggest)

import VueCookies from 'vue-cookies'
Vue.use(VueCookies)

import nodata from './component/nodata/nodata.vue'
Vue.component('no-data', nodata)

import GoogleLogin from 'vue-google-login';
Vue.component('google-login',GoogleLogin)

import facebookLogin from 'facebook-login-vuejs';
Vue.component('facebook-login',facebookLogin)

import vueDebounce from 'vue-debounce'
Vue.use(vueDebounce,{
  defaultTime: '1s'
})

import StarRating from 'vue-star-rating'
Vue.component('star-rating', StarRating);

import Notifications from 'vue-notification'
Vue.use(Notifications)

import VueMoment from 'vue-moment'
Vue.use(VueMoment)

new Vue({
  router,
  store,
  axios,
  apiRequest,
  render: h => h(App)
}).$mount('#app')
