<template>
    <!--Side bar Choose Delivery-->
    <div :class="$store.state.sidebar.chooseAddress ? 'active' : ''" class="mini_cart">
        <div class="cart_close mb-1">
            <div class="cart_text">
                <h3>Choose Address</h3>
            </div>
            <div class="mini_cart_close" @click="updateAddress()">
                <a href="javascript:void(0)"><i class="fa fa-check"></i></a>
            </div>
        </div>
        <div class="mb-5">
            <div class="row align-items-center my-3 mx-1 ishover" v-for="(data, index) in $store.state.listAddress" :key="'addr_' + index" @click="chosenAddress = index">
                <div class="col-12 card py-2" :class="chosenAddress == index ? 'address-sidebar-active' : ''">
                    <div class="row">
                        <div class="col-12">
                            <span><strong>{{ data.recipient_name }} ({{data.address_title}})</strong></span><br>
                            <span>{{data.phone}}</span><br>
                            <span>{{data.address_line1}}<br>{{data.city}}, {{data.state}}, {{data.country}}, {{data.pincode}}</span>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
    <!--Sidebar Choose Delivery end-->
</template>

<script>
import apiRequest from '@/helper/apiRequest.js'

export default {
    data () {
        return {
            chosenAddress: -1
        }
    },
    created () {
        
    },
    mounted () {
        
    },
    methods: {
        async updateAddress() {
            this.$store.state.showLoading = true
            this.$store.state.sidebar.chooseAddress = false
            if ( this.chosenAddress != -1){
                var body = {
                    "name" : this.$store.state.shoppingInvId,
                    "doctype": "Shopping Cart Invoice",
                    "address": this.$store.state.listAddress[this.chosenAddress].name,
                    "courier": "",
                    "service": "",
                    "eta": ""
                }
                var response = await apiRequest.createOrUpdateDoc(body)
                if(response.data != null){
                    this.$store.state.selectedAddress = this.$store.state.listAddress[this.chosenAddress]
                    this.$store.state.selectedDeliveryService = null
                }
                else {
                    alert(response.error)
                }
            }
            this.$store.state.showLoading = false
        },
    }
}
</script>