import VueHZoom from 'vue-h-zoom';
import Vue from 'vue'

Vue.use(VueHZoom);
Vue.component('originalVueZoom', VueHZoom);

const newZoom = Vue.component('originalVueZoom').extend({
    methods: {
        updateThumbnailPos: function () {
            const el = this.$refs.thumbnail;
                let elementDim = el.getBoundingClientRect();
                this.thumbnailPos = {
                top: elementDim.top / 2,
                left: elementDim.left / 2,
            }
        }
    }
});
export default newZoom