<template>
    <!--Side bar Choose Delivery-->
    <div :class="$store.state.sidebar.payment ? 'active' : ''" class="mini_cart">
        <div class="cart_close mb-1">
            <div class="cart_text">
                <h3>{{"sidebar.choosePayment"|translate}}</h3>
            </div>
            <div class="mini_cart_close" @click="$store.state.sidebar.payment = false">
                <a href="javascript:void(0)"><i class="ion-android-close"></i></a>
            </div>
        </div>
        <div class="mb-5">
            <div class="row align-items-center my-1 mx-1 ishover" v-for="(obj, index) in $store.state.listPayment" :key="'delivery_' + index" >          
                <div class="col-12 mt-3">
                    <span><b>{{ obj.item || '-' }}</b></span>
                </div>
                <div class="cart_item col-12 row my-1 ml-3" style="align-items: center;" v-for="(service, index) in obj.service" :key="'service_' + index" @click="choosePayment(service)">
                    <div class="col-3 ">
                        <!-- Image -->
                        <div class="square-img-container" style="width: 85%">
                            <img id="deliveryImage" :src="$store.state.baseURL + service.image" class="card" alt="logo" width="100%">
                        </div>
                    </div>
                    <div class="col-7" >
                        <!-- Title / Text -->
                        {{ service.payment_method }}
                    </div>
                    <div class="col-2">
                        <!-- Radio button -->
                        <p-check class="p-tada p-locked p-plain p-icon p-round p-bigger" :checked="paymentServiceSelected === service">
                            <i class="icon lnr lnr-checkmark-circle" slot="extra"></i>
                        </p-check>
                    </div>
                </div>
            </div>
        </div>
        <transition name="slide">
            <div class="cart_links_btn mt-4" v-show="paymentServiceSelected != null" @click="applyPayment()">
                <a><b>Apply</b></a>
            </div>
        </transition>
    </div>
    <!--Sidebar Choose Delivery end-->
</template>

<script>
import apiRequest from '@/helper/apiRequest.js'

export default {
    data () {
        return {
            paymentServiceSelected: null
        }
    },
    created () {
        
    },
    mounted () {
        
    },
    methods: {
        async applyPayment() {
            this.$store.state.showLoading = true
            this.$store.state.sidebar.payment = false
            var body = {
                "name" : this.$store.state.shoppingInvId,
                "doctype": "Shopping Cart Invoice",
                "payment_method": this.paymentServiceSelected.name
            }
            var response = await apiRequest.createOrUpdateDoc(body)
            if(response.data != null){
                this.$store.state.selectedPayment = this.paymentServiceSelected
                this.paymentServiceSelected = null
            }
            else {
                this.$notify({
                    group: 'notification',
                    type: 'danger',
                    title: 'Oops...',
                    text: response.error
                });
            }
            this.$store.state.showLoading = false
        },
        choosePayment (data) {
            this.paymentServiceSelected = data
        }
    }
}
</script>