<template>
  <div class="container">
      <div class="col-12">
          <div class="row mb-5 mt-4">
            <div class="section_title col-12 px-0">
                <h2><span> <strong>{{ "checkout.checkout"|translate }}</strong></span></h2>
            </div>
            <div class="col-lg-8 col-sm-12 mb-3">
                <div class="row card py-3">
                    <div class="col-12">
                        <div class="row">
                            <div class="col-8">
                                <h4>{{ "checkout.shippingAddress"|translate }}</h4>
                            </div>
                            <div class="col-4 text-right">
                                <a v-if="$store.state.selectedAddress == null" @click="chooseAddress()">{{ "checkout.addAddress"|translate }}</a>
                                <a v-else  @click="$store.state.sidebar.chooseAddress = true">{{ "checkout.changeAddress"|translate }}</a>
                            </div>
                        </div>
                        <div class="line mt-2 mb-3"></div>
                        <div v-if="selectedAddress == null">
                            <h5>{{"checkout.noAddressData"| translate}}</h5>
                        </div>
                        <div v-else>
                            <h4>{{selectedAddress.recipient_name}} ({{selectedAddress.address_title}})</h4>
                            <h5>{{selectedAddress.phone}}<br>
                            {{selectedAddress.address_line1}}<br>{{selectedAddress.city}}, {{selectedAddress.state}}, {{selectedAddress.country}}, {{selectedAddress.pincode}}</h5>
                        </div>
                    </div>
                </div>
                <div class="row card mt-3">
                    <div class="col-12">
                        <div class="row mt-3" v-for="(data, index) in dataItems" :key="index">
                            <div class="col-lg-2 col-md-3 col-sm-3">
                                <img v-if="data.image" :src="$store.state.baseURL + encodeURIComponent(data.image)" width="100%" class="card"/>
                                <img v-else src="@/assets/noimage.jpg" width="100%" class="card"/>
                            </div>
                            <div class="col-lg-10 col-md-9 col-sm-9">
                                <div class="row">
                                    <div class="col-12">
                                            <h4><strong>{{ data.item_name || ''}}</strong></h4>
                                            <div class="price_box" v-if="'flash_sale' in data">
                                                <span class="old_price">{{ getCurrency(parseFloat(data.price)) }}</span><br/>
                                                <span class="current_price">{{ getCurrency(parseFloat(data.price -data.margin)) }}</span>
                                            </div>
                                            <div class="price_box" v-else>
                                                <span class="current_price">{{ getCurrency(parseFloat(data.price)) }}</span>
                                            </div>
                                            <h5 class="mt-2">{{ data.qty }} {{  "checkout.item"| translate }} ({{ data.weight_per_unit }} {{ 'weight_uom' in data ? data['weight_uom'] : 'Gram' }})</h5>
                                    </div>
                                </div>
                            </div>
                            <div class="col-12 text-right mt-3">
                                <div class="line" v-if="index != dataItems.length-1"></div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="row card py-3 mt-3">
                    <div class="col-12">
                        <div class="row">
                            <div class="col-8">
                                <h4>{{ "checkout.deliveryService"|translate }}</h4>
                            </div>
                            <div class="col-4 text-right">
                                <a @click="fetchService()">{{ "checkout.chooseService"|translate }}</a>
                            </div>
                        </div>
                        <transition name="slide">
                            <div v-if="$store.state.selectedDeliveryService != null">
                                <div class="line mt-2 mb-3"></div>
                                <div class="row">
                                    <div class="col-md-1 col-sm-2 pr-0">
                                        <img id="deliveryImage" :src="$store.state.baseURL + encodeURIComponent($store.state.selectedDeliveryService.image)" alt="logo" class="card" width="100%">
                                    </div>
                                    <div class="col-md-11 col-sm-10">
                                        <h4>{{$store.state.selectedDeliveryService.title}}</h4>
                                        <h5>{{$store.state.selectedDeliveryService.service.type}}</h5>
                                    </div>
                                </div>
                            </div>
                        </transition>
                    </div>
                </div>
                <div class="row card py-3 mt-3">
                    <div class="col-12">
                        <div class="row">
                            <div class="col-8">
                                <h4>{{ "checkout.paymentMethod"|translate }}</h4>
                            </div>
                            <div class="col-4 text-right">
                                <a @click="fetchPayment()">{{ "checkout.choosePayment"|translate }}</a>
                            </div>
                        </div>
                        <transition name="slide">
                            <div v-if="$store.state.selectedPayment != null">
                                <div class="line mt-2 mb-3"></div>
                                <div class="row">
                                    <div class="col-md-1 col-sm-2 pr-0">
                                        <img id="deliveryImage" :src="$store.state.baseURL + encodeURIComponent($store.state.selectedPayment.image)" alt="logo" class="card" width="100%">
                                    </div>
                                    <div class="col-md-11 col-sm-10">
                                        <h4>{{$store.state.selectedPayment.payment_method || '-'}}</h4>
                                        <h5>{{$store.state.selectedPayment.category || '-'}}</h5>
                                    </div>
                                </div>
                            </div>
                        </transition>
                    </div>
                </div>
                <div class="row card py-3 mt-3">
                    <div class="col-12">
                        <h4>{{ "checkout.note"|translate }}</h4>
                        <div class="line my-3 form-group"></div>
                         <textarea class="form-control" rows="5" v-model="note" placeholder="Let us know what can we do to satisfy your needs :)"></textarea>
                    </div>
                </div>
            </div>
            <div class="col-lg-4 col-sm-12 mb-3">
                <div class="sticky-right">
                    <div class="row card pt-3">
                        <div class="col-12">
                            <h4>{{ "checkout.voucher"|translate }}</h4>
                            <div class="line my-3"></div>
                            <p>{{ "checkout.enterYourCoupon"|translate }}</p>
                        </div>
                        <div class="row coupon_inner">
                            <div class="col-lg-8 col-7" style="padding-right:0px !important;">
                                <input placeholder="Coupon code" v-model="couponCode" class="uppercase" type="text">
                            </div>
                            <div class="col-lg-4 col-5" style="padding-left:0px !important;" @click="applyVoucher()">
                                <button>{{ "checkout.apply"|translate }}</button>
                            </div>
                        </div>
                    </div>
                    <div class="row card pt-3 mt-3">
                        <div class="col-12">
                            <h4>{{ "checkout.summary"|translate }}</h4>
                            <div class="row">
                                <div class="col-7">
                                    <p>{{ "checkout.totalPrice"|translate }} ({{countItem()}} {{ "checkout.item"|translate }})</p>
                                </div>
                                <div class="col-5 text-right">
                                    <p>{{getCurrency(parseFloat(countItemPrice()))}}</p>
                                </div>
                            </div>
                            <transition name="slide">
                                <div class="row" v-if="$store.state.selectedDeliveryService != null">
                                    <div class="col-7">
                                        <p>{{ "checkout.deliveryFee"|translate }}</p>
                                    </div>
                                    <div class="col-5 text-right">
                                        <p>{{getCurrency(parseFloat($store.state.selectedDeliveryService.service.price))}}</p>
                                    </div>
                                </div>
                            </transition>
                            <transition name="slide">
                                <div class="row" v-if="discount != 0.0">
                                    <div class="col-7">
                                        <p>{{ "checkout.discount"|translate }} ({{ appliedVoucherCode }})</p>
                                    </div>
                                    <div class="col-5 text-right">
                                        <p>-{{getCurrency(discount)}}</p>
                                    </div>
                                </div>
                            </transition>
                            <transition name="slide">
                                <div class="row" v-if="convenienceFee != 0.0">
                                    <div class="col-7">
                                        <p>{{ "checkout.convenienceFee"|translate }}</p>
                                    </div>
                                    <div class="col-5 text-right">
                                        <p>{{getCurrency(convenienceFee)}}</p>
                                    </div>
                                </div>
                            </transition>
                            <div class="line my-2"></div>
                            <div class="row">
                                <div class="col-7">
                                    <h4>{{ "checkout.total"|translate }}</h4>
                                </div>
                                <div class="col-5 text-right">
                                    <h4 class="price-color">{{getCurrency(parseFloat(countTotalPrice()))}}</h4>
                                </div>
                            </div>
                            <div @click="$store.state.selectedDeliveryService != null && $store.state.selectedAddress != null && $store.state.selectedPayment != null ? payNow() : ''" class="mt-2" :class="$store.state.selectedDeliveryService != null && $store.state.selectedAddress != null && $store.state.selectedPayment != null ? 'cart_links_btn': 'cart_links_btn_disabled'">
                                <a><b>{{ "checkout.payNow"|translate }}</b></a>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
      </div>
      
  </div>
</template>

<script>
import apiRequest from '@/helper/apiRequest.js'
import currency from '@/helper/currency.js'
import helper from '@/helper/global.js'

export default {
  name: 'Blog',
  components: { 
      
    },
  data () {
    return {
      page: 0,
      note: '',
      dataItems: [],
      discount: 0.0,
      couponCode: '',
      appliedVoucherCode: '',
      convenienceFee: 0.0
    }
  },
  created () {
    this.resetCheckoutState()
    this.$store.state.showLoading = false
    if (apiRequest.isGuest()){
        location.href = '/#/auth'
    }
    else {
        this.createShoppingCartInvoice()
        this.fetchAddress()
    }
  },
  computed: {
      selectedAddress() {
        return this.$store.state.selectedAddress
      }
  },
  methods: {
    countItem () {
        var count = 0
        this.dataItems.forEach(el => {
            count += parseInt(el.qty)
        })
        return count
    },
    calculateDiscount (discount, amount) {
        return helper.calculateDiscount(discount, amount)
    },
    resetCheckoutState(){
        this.$store.state.selectedAddress = null,
        this.$store.state.listAddress= [],
        this.$store.state.selectedDeliveryService= null,
        this.$store.state.listDeliveryService= [],
        this.$store.state.shoppingInvId= '',
        this.$store.state.listPayment= [],
        this.$store.state.selectedPayment= null
    },
    async createShoppingCartInvoice() {
        this.$store.state.showLoading = true
        this.$store.state.shoppingInvId = ''
        var response = await apiRequest.createShoppingCartInvoice({})
        if(response.data != null){
            this.dataItems = response.data.items
            this.$store.state.shoppingInvId = response.data.name
            console.log('this.dataItems',this.dataItems)
        }
        else {
            this.$notify({
                group: 'notification',
                type: 'danger',
                title: 'Oops...',
                text: response.error
            });
            this.$router.push({ name: 'cart'})
        }
        this.$store.state.showLoading = false
    },
    async fetchAddress() {
        var body = {
            "identifier" : "Get Address",
            "limit": 9999
        }
        var response = await apiRequest.requestRead(body)
        if(response.data != null){
            this.$store.state.listAddress = response.data
            for( let index in response.data) {
                if(response.data[index].is_default == 1) {
                    this.$store.state.selectedAddress = response.data[index]
                }
            }
        }
        else {
            this.$notify({
                group: 'notification',
                type: 'danger',
                title: 'Oops...',
                text: response.error
            });
        }
    },
    async applyVoucher() {
        this.$store.state.showLoading = true
        var body = {
            "shopping_cart_invoice" : this.$store.state.shoppingInvId,
            "voucher": this.couponCode
        }
        var response = await apiRequest.applyVoucher(body)
        if(response.data != null){
            this.discount = parseFloat(response.data.total_discount)
            this.appliedVoucherCode = response.data.voucher
        }
        else {
            this.$notify({
                group: 'notification',
                type: 'danger',
                title: 'Oops...',
                text: response.error
            });
        }
        this.$store.state.showLoading = false
    },
    countTotalWeight() {
        var weight = 0.0

        this.dataItems.forEach(el => {
            if (el.weight_per_unit == 0) {
                weight += 1.0 * parseInt(el.qty)
            } else {
                if ('weight_uom' in el) {
                    if (el.weight_uom == 'Gram' || el.weight_uom == 'gram') {
                        weight += parseInt(el.qty) * (el.weight_per_unit / 1000.0)
                    } else {
                        weight += parseInt(el.qty) * el.weight_per_unit
                    }
                } else {
                    weight += 1.0 * parseInt(el.qty)
                }
            }
        })

        return weight <= 0 ? 1 : Math.ceil(weight)
    },
    async fetchService() {
        if (this.$store.state.selectedAddress == null) {
            this.$notify({
                group: 'notification',
                type: 'danger',
                title: 'Oops...',
                text: 'notification.chooseAddressFirst'
            });
            return
        }
        this.$store.state.showLoading = true
        this.$store.state.listDeliveryService = []
        var body = {
            "address" : this.$store.state.selectedAddress.name,
            "weight": this.countTotalWeight(),
            "shopping_cart_invoice": this.$store.state.shoppingInvId
        }
        console.log("Body", body)
        var response = await apiRequest.getTarif(body)
        if(response.data != null){
            for(let idx in response.data){
                var courier = {
                    'name': response.data[idx].delivery_method,
                    'title': response.data[idx].delivery_method,
                    'image': response.data[idx].image,
                    'service': response.data[idx].pricelist
                }
                this.$store.state.listDeliveryService.push(courier)
            }
            this.$store.dispatch('openSideBar', {
                'sidebar': 'chooseDelivery',
                'open': true,
                'callback': () => {
                    // TODO
                }
            })
        }
        else {
            this.$notify({
                group: 'notification',
                type: 'danger',
                title: 'Oops...',
                text: response.error
            });
        }
        this.$store.state.showLoading = false
    },
    async fetchPayment() {
        this.$store.state.showLoading = true
        this.$store.state.listPayment = []
        var response = await apiRequest.getPaymentList(this.countTotalPrice())
        if(response.data != null){
            for(let idx in response.data.availability){
                var group = {
                    'item': response.data.availability[idx].item,
                    'image': response.data.availability[idx].image,
                    'service': response.data[response.data.availability[idx].item]
                }
                this.$store.state.listPayment.push(group)
            }
            this.$store.state.sidebar.payment = true
        }
        else {
            this.$notify({
                group: 'notification',
                type: 'danger',
                title: 'Oops...',
                text: response.error
            });
        }
        this.$store.state.showLoading = false
    },
    getCurrency(money) {
        return currency.format('IDR', money)
    },
    countItemPrice() {
        var price = 0
        for(let idx in this.dataItems){
            price += this.dataItems[idx].total
        }
        return price
    },
    countTotalPrice() {
        // Count Item Price
        var price = this.countItemPrice()
        // Count Delivery Service
        if (this.$store.state.selectedDeliveryService != null){
            price += parseFloat(this.$store.state.selectedDeliveryService.service.price)
        }
        // Count Discount
        price -= this.discount

        // Count Confenience Fee
        if(this.$store.state.selectedPayment != null){
            var total = price
            console.log(total)
            this.convenienceFee = Math.ceil(eval(this.$store.state.selectedPayment.fee_calculation))
        }
        else {
            this.convenienceFee = 0.0
        }
        price += this.convenienceFee

        return price
    },
    chooseAddress() {
        this.$store.dispatch('openSideBar', {
            'sidebar': 'addAddress',
            'open': true,
            'callback': () => {
                this.fetchAddress()
            }
        })
    },
    async payNow() {
        this.$store.state.showLoading = true
        // UPDATE CART
        var body = {
            "docstatus" : 1,
            "name" : this.$store.state.shoppingInvId,
            "doctype": "Shopping Cart Invoice",
            "payment_method": this.$store.state.selectedPayment.payment_method,
            "note": this.note,
            "address": this.$store.state.selectedAddress.name,
            "eta": this.$store.state.selectedDeliveryService.service.etd_from + " - " + this.$store.state.selectedDeliveryService.service.etd_thru + " days",
            "courier_service": this.$store.state.selectedDeliveryService.service.type,
            "from_territory": this.$store.state.selectedDeliveryService.service.from_territory,
            "to_territory": this.$store.state.selectedDeliveryService.service.to_territory,
            "courier": this.$store.state.selectedDeliveryService.name,
            "shipping_fee": this.$store.state.selectedDeliveryService.service.price,
        }
        // console.log("PAYOUT", this.$store.state.selectedDeliveryService)
        if (this.couponCode != '') {
            body.voucher_code = this.couponCode
        }
        // console.log("body",JSON.stringify(body))
        var response = await apiRequest.updateDoc(body)
        if(response.error != null){
            this.$notify({
                group: 'notification',
                type: 'danger',
                title: 'Oops...',
                text: response.error
            });
        }
        else {
            // window.open(response.data.xendit_invoice_url, "_blank")
            location.href = '/#/payment?uri='+encodeURI(response.data.xendit_invoice_url)
            this.resetCheckoutState()
            // this.$router.back()
        }
        this.$store.state.showLoading = false
    }
  }
}
</script>
