<template>
  <div>
    <!-- customer login start -->
    <div class="customer_login mt-32">
        <div class="container">
            <div class="row">
                <!--login area start-->
                <div class="col-lg-6 col-md-6">
                    <div class="account_form sticky-left">
                        <h2>login</h2>
                        <form>
                            <p>
                                <label>Email <span>*</span></label>
                                <input v-model="login.email" type="email" @keyup.enter="setFocus('login-password')">
                            </p>
                            <p>
                                <label>Passwords <span>*</span></label>
                                <input id="login-password" v-model="login.password" type="password" @keyup.enter="doLogin">
                            </p>
                            <div class="login_submit">
                                <a @click="gotoForgot">Lost your password?</a>
                                <div class="auth_button" @click="doLogin">login</div>
                            </div>
                        </form>
                        <div class="row justify-content-center my-3">
                            <div class="col-lg-2 col-md-3 col-3">
                                <hr>
                            </div>
                            <div class="col-lg-1 col-md-1 col-1" style="align-self: center;">
                                <span>or</span>
                            </div>
                            <div class="col-lg-2 col-md-3 col-3">
                                <hr>
                            </div>
                        </div>
                        <div class="row justify-content-center">
                            <div class="col-lg-6 col-12 text-center mb-2">
                                <google-login :params="params" :onSuccess="onSuccess" :onFailure="onFailure" class="btn-sso m-0">
                                    <img src="/img/logo/google_logo.png" class="mr-2" width="18px"/>
                                    <span style="margin-top: auto; margin-bottom:auto;">Continue with Google</span>
                                </google-login>
                            </div>
                            <div class="col-lg-6 col-12 text-center mb-2">
                                <facebook-login id="btn-facebook" @sdk-loaded="sdkLoaded" appId="1470770786426555" :loginOptions="{ scope: 'email', return_scopes: true }" @login="getUserData" @get-initial-status="getUserData" class="w-100 btn-facebook-sso m-0">
                                </facebook-login>
                            </div>
                        </div>
                    </div>
                    <!-- <google-login :logoutButton=true :onSuccess="googleLogout(true)" :onFailure="googleLogout(false)">Logout</google-login> -->
                </div>
                <!--login area start-->

                <!--register area start-->
                <div class="col-lg-6 col-md-6">
                    <div class="account_form register">
                        <h2>Register</h2>
                        <form action="#">
                            <p>
                                <label>Full Name <span>*</span></label>
                                <input v-model="register.customer_name" type="text" @keyup.enter="setFocus('register-email')">
                            </p>
                            <p>
                                <label>Email address <span>*</span></label>
                                <input id="register-email" v-model="register.email_id" type="text" @keyup.enter="setFocus('register-phone')">
                            </p>
                            <p>
                                <label>Phone <span>*</span></label>
                                <input id="register-phone" v-model="register.mobile_no" type="number" @keyup.enter="setFocus('register-password')">
                            </p>
                            <p>
                                <label>Password <span>*</span></label>
                                <input id="register-password" v-model="register.password" type="password" @keyup.enter="setFocus('register-confirm-password')">
                            </p>
                            <p>
                                <label>Confirm Password <span>*</span></label>
                                <input id="register-confirm-password" v-model="register.confirm_password" type="password" @keyup.enter="doRegister">
                            </p>
                            <div class="login_submit">
                                <div class="auth_button" @click="doRegister">register</div>
                            </div>
                        </form>
                    </div>
                </div>
                <!--register area end-->
            </div>
        </div>
    </div>
    <!-- customer login end -->
  </div>
</template>

<script>
import apiRequest from '@/helper/apiRequest.js'
// import facebookLogin from 'facebook-login-vuejs'
// import Vue from 'vue'

export default {
  name: 'Account',
  components: { 
      
   },
  data () {
    return {
        login: {
            email: '',
            password: ''
        },
        register: {
            password: '',
            mobile_no: '',
            customer_name: '',
            email_id: '',
            confirm_password: ''
        },
        params: {
            client_id: "43039717645-i864gr78bsj7bpnkp0jb2gsel004it8d.apps.googleusercontent.com"
        },
        renderParams: {
            width: 250,
            height: 50,
            longtitle: true
        },
        user: {},
        FB: null
    }
  },
  created () {
        this.$store.state.showLoading = false
        this.checkCookies()
  },
  mounted(){
    var btn = document.getElementById("btn-facebook").querySelector('button')
    var googleBtn = document.getElementById("google-signin-btn-0")

    btn.classList.add('btn-block')
    googleBtn.classList.add('btn-block')

    btn.attr("type", "button")
    googleBtn.attr("type", "button")
  },
  methods: {
    checkCookies() {
        console.log(apiRequest.isGuest())
        if (!apiRequest.isGuest()) {
            location.href = '/'
        }
    },
    async doLogin() {
        this.$store.state.showLoading = true
        var body = {
            'usr': this.login.email || this.register.email_id,
            'pwd': this.login.password || this.register.password
        }
        var response = await apiRequest.login(body)
        if(response.data != null){
            console.log("RESPONSE LOGIN", response.data)
            localStorage.setItem('isAlreadyLogin', "1")
            location.reload()
        }
        else {
            localStorage.setItem('isAlreadyLogin', "0")
            this.$notify({
                group: 'notification',
                type: 'danger',
                title: 'Oops...',
                text: response.error
            });
        }
        this.$store.state.showLoading = false
    },
    async doRegister () {
        this.$store.state.showLoading = true
        if (this.register.password == this.register.confirm_password) {
            var body = {
                'password': this.register.password,
                'mobile_no': this.register.mobile_no,
                'customer_name': this.register.customer_name,
                'email_id': this.register.email_id,
                'doctype': 'Customer'
            }
            var response = await apiRequest.createDoc(body)
            if(response.data != null){
                this.doLogin()
            }
            else {
                this.$notify({
                    group: 'notification',
                    type: 'danger',
                    title: 'Oops...',
                    text: response.error
                });
            }
        } 
        else {
            this.$notify({
                group: 'notification',
                type: 'danger',
                title: 'Oops...',
                text: 'Password tidak sama'
            });
        }
        this.$store.state.showLoading = false
    },
    onSuccess(googleUser) {
        // This only gets the user information: id, name, imageUrl and email
        console.log(googleUser.getBasicProfile());
        
        var data = googleUser.getBasicProfile()
        // console.log("GOOGLE DATA --", data)
        this.doLoginSSO(data.$t, data.NT, data.Ad, "Google")
    },
    onFailure() {
        
    },
    sdkLoaded(payload) {
        this.FB = payload.FB
        // console.log(payload)
        if (payload.isConnected) {
            this.getUserData()
        }
    },
    getUserData(){

        this.getUserFBData()
    },
    async getUserFBData() {
        if (this.FB) {
            this.$store.state.showLoading = true
            await this.FB.api('/me', 'GET', { fields: 'id,name,email,picture' },
                user => {
                    this.$store.state.showLoading = false
                    this.user = user
                    this.doLoginSSO(this.user.email, this.user.id, this.user.name, "Facebook")
                    this.facebookLogout()
                }
            )
        }
    },
    async doLoginSSO(email, pwd, name, sso) {
        this.$store.state.showLoading = true
        var body = {
            'usr': email,
            'pwd': pwd
        }
        var response = await apiRequest.login(body)
        if(response.data != null){
            localStorage.setItem('isAlreadyLogin', "1")
            location.reload()
        }
        else {
            this.doRegisterSSO(email,pwd,name,sso)
        }
    },
    async doRegisterSSO (email,pwd,name,sso) {
        this.$store.state.showLoading = true
        var body = {
            'password': pwd,
            'customer_name': name,
            'email_id': email,
            'register_via': sso,
            'doctype': 'Customer'
        }
        // console.log('SSO BODY -- ', body)
        var response = await apiRequest.createDoc(body)
        if(response.data != null){
            this.doLoginSSO(email,pwd,name,sso)
        }
        else {
            if(sso == "Google"){
                this.$notify({
                    group: 'notification',
                    type: 'danger',
                    title: 'Oops...',
                    text: 'notification.yourAccountHasBeenRegisteredGoogle'
                });
            }
            else {
                this.$notify({
                    group: 'notification',
                    type: 'danger',
                    title: 'Oops...',
                    text: 'notification.yourAccountHasBeenRegisteredFacebook'
                });
            }
        }
        this.$store.state.showLoading = false
    },
    googleLogout(aa){
        alert(aa)
    },
    facebookLogout() {
        this.FB.logout(function () {
            console.log("FB Logout")
        })
    },
    setFocus(id) {
        document.getElementById(id).focus()
    },
    gotoForgot() {
        this.$router.push('/forgot')
    }
  }
}
</script>
