<template>
    <!--Side bar Choose Delivery-->
    <div :class="$store.state.sidebar.salesInvoice ? 'active' : ''" class="mini_cart">
        <div class="cart_close mb-1">
            <div class="cart_text">
                <h3>{{"salesInvoice.salesInvoice"|translate}}</h3>
            </div>
            <div class="mini_cart_close" @click="closeSidebar">
                <a href="javascript:void(0)"><i class="fa fa-close"></i></a>
            </div>
        </div>

        <!-- Invoice Info -->
        <div class="row ml-0 mr-0 mt-2 border-bottom-line">
            <div class="col-6 mb-3 pl-0 lh-12">
                {{ "salesInvoice.invoiceNumber" | translate }} <br/><span><b>{{ invoice.name || '-' }}</b></span>
            </div>
            <div class="col-6 mb-3 lh-12">
                Status <br/>
                <b v-if="invoice.shipping_status == 'Pending'"><b v-if="invoice.paid == 1">Paid</b><b v-else>Pending</b></b>
                <b v-else>{{invoice.shipping_status}}</b>
            </div>
            <div class="col-6 mb-3 lh-12 pl-0">
                {{ "salesInvoice.purchaseDate" | translate }} <br/><b>{{ invoice.posting_date | moment('DD MMMM YYYY') }}</b>
            </div>
            <div class="col-12 px-0 mb-3" v-if="needPayment">
                <div class="btn btn-danger btn-block" @click="toPayment(invoice.xendit_invoice_url)">{{ "salesInvoice.payNow" | translate }}</div>
            </div> 
            <div class="col-12 px-0 mb-3" v-else-if="canReview">
                <div class="btn btn-outline-danger btn-block" @click="toReview(invoice.name)">{{ "salesInvoice.addReview" | translate }}</div>
            </div> 
            <div class="col-12 px-0 mb-3" v-else-if="waitingConfirmation">
                <div class="row">
                    <div class="col-6">
                        <div class="btn btn-outline-danger btn-block" @click="complaint(invoice.name)">{{ "salesInvoice.complaint" | translate }}</div>
                    </div>
                    <div class="col-6">
                        <div class="btn btn-danger btn-block" @click="clickFinish()">{{ "salesInvoice.finishInvoice" | translate }}</div>
                    </div>
                </div>
            </div> 
        </div>
        <!-- Delivery Info -->
        <div class="row ml-0 mr-0 mt-2 border-bottom-line" v-if="address != null">
            <div class="col-6 mb-3 pl-0 lh-12">
                {{ "salesInvoice.delivery" | translate }} <br/><span><b>{{ invoice.courier }} - {{ invoice.courier_service }}</b></span>
            </div>
            <div class="col-6 mb-3 lh-12">
                {{ "salesInvoice.deliveryReceipt" | translate }} <br/><b>{{ invoice.delivery_receipt || '-' }}</b>
            </div>
            <div class="col-12 mb-3 lh-12 pl-0">
                {{ "salesInvoice.sendTo" | translate }}: <br/><b>{{ address.recipient_name }}</b>
                <br/>
                {{ address.address_line1 }}
                <br/>
                {{ address.city }}, {{ address.state }}, {{ address.country }}
                <br/>
                {{ address.pincode }}
                <br v-if="address.pincode"/>
                {{ "salesInvoice.phoneNumber" | translate }}: {{ address.phone }}
            </div>
        </div>

        <!-- Invoice Detail -->
        <div class="mt-2 border-bottom-line">
            <div class="mb-3"><b>{{ "salesInvoice.productList" | translate }}</b></div>
            <div class="row ml-0 mr-0 mb-3" v-for="(obj, index) in items" :key="'product_si_' + index">
                <div class="col-lg-3 col-md-3 col-sm-3 pl-0 pr-0">
                    <img v-if="obj.image" :src="$store.state.baseURL + obj.image" width="100%" class="card"/>
                    <img v-else src="@/assets/noimage.jpg" width="100%" class="card"/>
                </div>
                <div class="col-lg-9 col-md-9 col-sm-9">
                    <div class="row">
                        <div class="col-12">
                            <h5><strong>{{ obj.item_name }}</strong></h5>
                            <h6 class="mt-2">{{ obj.qty }} {{ "salesInvoice.item" | translate }}<span>&nbsp;&nbsp;x&nbsp;&nbsp;{{ currency(obj.rate) }}</span></h6>
                            <div class="price_box">
                                <span class="current_price">{{ currency(obj.amount) }}</span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>

        <!-- Payment Info -->
        <div class="row ml-0 mr-0 mt-2 border-bottom-line">
            <div class="col-12 mb-3 pl-0 lh-12">
                <b>{{ "salesInvoice.payment" | translate }}</b>
            </div>
            <div class="col-6 pl-0 lh-12">
                {{ "salesInvoice.paymentMethod" | translate }}
            </div>
            <div class="col-6 h-12 ">
                <b>{{ invoice.payment_method }}</b>
            </div>
            <div class="col-6 pl-0 lh-12">
                Subtotal
            </div>
            <div class="col-6 h-12">
                <b>{{ currency(invoice.total) }}</b>
            </div>
            <div class="col-6 pl-0 lh-12" v-if="shipping_fee != null">
                {{ "salesInvoice.deliveryFee" | translate }}
            </div>
            <div class="col-6 h-12" v-if="shipping_fee != null">
                <b>{{ currency(shipping_fee.tax_amount) }}</b>
            </div>
            <div class="col-6 pl-0 lh-12" v-if="invoice.discount_amount > 0">
                {{ "salesInvoice.discount" | translate }}
            </div>
            <div class="col-6 h-12 text-danger" v-if="invoice.discount_amount > 0">
                <b>{{ currency(invoice.discount_amount) }}</b>
            </div>
            <div class="col-6 pl-0 lh-12" v-if="convenience_fee != null">
                {{ "salesInvoice.convenienceFee" | translate }}
            </div>
            <div class="col-6 h-12" v-if="convenience_fee != null">
                <b>{{ currency(convenience_fee.tax_amount) }}</b>
            </div>
        </div>

        <!-- Payment Info -->
        <div class="row ml-0 mr-0 mt-2">
            <div class="col-6 mb-1 pl-0 lh-12">
                <h4><b>Total</b></h4>
            </div>
            <div class="col-6 mb-1 h-12">
                <b>{{ currency(invoice.grand_total) }}</b>
            </div>
        </div>
    </div>
</template>

<script>
import currency from '@/helper/currency.js'

export default {
    data () {
        return {
            chosenAddress: -1
        }
    },
    created () {
        
    },
    computed: {
        invoice () {
            return this.$store.state.invoice
        },
        address () {
            if (this.$store.state.invoice.address_detail.length > 0) {
                return this.$store.state.invoice.address_detail[0]
            }
            return null
        },
        items () {
            if (this.$store.state.invoice.items.length > 0) {
                return this.$store.state.invoice.items
            }
            return []
        },
        shipping_fee () {
            var item = null
            if (this.$store.state.invoice.taxes.length > 0){
                this.$store.state.invoice.taxes.forEach(tax => {
                    if (tax.description == "Shipping Fee") {
                        item = tax
                    }
                })
            }
            return item
        },
        convenience_fee () {
            var item = null
            if (this.$store.state.invoice.taxes.length > 0){
                this.$store.state.invoice.taxes.forEach(tax => {
                    if (tax.description == "Payment Fee") {
                        item = tax
                    }
                })
            }
            return item
        },
        needPayment () {
            var bool = false

            if (this.$store.state.invoice.shipping_status == "Pending" && this.$store.state.invoice.paid == 0){
                bool = true
            }

            return bool
        },
        canReview () {
            var bool = false

            if (this.$store.state.invoice.shipping_status == "Completed") {
                bool = true
            }

            return bool
        },
        waitingConfirmation () {
            var bool = false

            if (this.$store.state.invoice.shipping_status == "Shipped") {
                bool = true
            }

            return bool
        }
    },
    mounted () {
        
    },
    methods: {
        complaint () {
            this.$store.state.sidebar.salesInvoice = false
            if (this.$store.state.sidebarEvents) {
                this.$store.state.sidebarEvents.didClickComplaint(this.$store.state.invoice.name)
            }
        },
        clickFinish () {
            this.$store.state.sidebar.salesInvoice = false
            if (this.$store.state.sidebarEvents) {
                this.$store.state.sidebarEvents.didClickFinish(this.$store.state.invoice.name)
            }
        },
        toPayment (data) {
            this.closeSidebar()
            location.href = '/#/payment?uri='+encodeURI(data)
        },
        toReview (data) {
            this.closeSidebar()
            location.href = '/#/rating/'+encodeURI(data)
        },
        currency (amount) {
            return currency.format("IDR", amount)
        },
        closeSidebar() {
            this.$store.dispatch('openSideBar', {
                'sidebar': 'salesInvoice',
                'open': false,
                'callback': () => {}
            })
        }
    }
}
</script>