<template>
  <div>
      <!-- my account start  -->
    <section class="main_content_area">
        <div class="container">
            <div class="account_dashboard">
                <div class="row">
                    <div class="col-sm-12 col-md-3 col-lg-3">
                        <!-- Nav tabs -->
                        <div class="dashboard_tab_button">
                            <ul role="tablist" class="nav flex-column dashboard-list">
                                <li><a ref="profileNav" href="#profile" @click="getCustomer" data-toggle="tab" class="nav-link active">{{ "account.myProfile.myProfile" | translate }}</a></li>
                                <li><a ref="addressNav" href="#address" @click="getAddress" data-toggle="tab" class="nav-link">{{ "account.addresses.addresses" | translate }}</a></li>
                                <li><a ref="invoiceNav" href="#orders" @click="getSalesInvoice" data-toggle="tab" class="nav-link">{{ "account.orderAndTracking.orderAndTracking" | translate }}</a></li>
                                <li><a @click="doLogout" class="nav-link">{{ "account.logout.logout" | translate }}</a></li>
                            </ul>
                        </div>
                    </div>
                    <div class="col-sm-12 col-md-9 col-lg-9">
                        <!-- Tab panes -->
                        <div class="tab-content dashboard_content">
                            <div class="tab-pane fade show active" id="profile">
                                <div class="section_title col-12 px-0">
                                    <h2><span> <strong>{{ "account.myProfile.myProfile" | translate }}</strong></span></h2>
                                </div>
                                <div class="login">
                                    <div class="login_form_container">
                                        <div class="account_login_form">
                                            <form>
                                                <label>{{ "account.myProfile.fullName" | translate }}</label>
                                                <input type="text" name="full-name" v-model="customer.first_name">

                                                <label>{{ "account.myProfile.birthdate" | translate }}</label>
                                                <input type="date" name="birthdate" v-model="customer.birth_date">
                                                
                                                <label>{{ "account.myProfile.gender" | translate }}</label>
                                                <!-- <input type="text" name="gender" v-model="customer.gender"> -->
                                                <select name="gender" v-model="customer.gender">
                                                    <option value="Male">{{ "account.myProfile.male" | translate }}</option>
                                                    <option value="Female">{{ "account.myProfile.female" | translate }}</option>
                                                </select>

                                                <label>{{ "account.myProfile.phoneNumber" | translate }}</label>
                                                <input type="number" name="phone-number" v-model="customer.mobile_no">

                                                <label>{{ "account.myProfile.email" | translate }}</label>
                                                <input read-only type="text" name="email" v-model="customer.name">
                                                <!-- <label>{{ "account.myProfile.birthdate" | translate }}</label>
                                                <input type="date" placeholder="MM/DD/YYYY" value="" name="birthday"> -->
                                                <div class="save_button primary_btn default_button">
                                                    <button @click="updateCustomer">{{ "account.myProfile.save" | translate }}</button>
                                                </div>
                                            </form>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div class="tab-pane fade" id="address">
                                <div class="section_title col-12 px-0">
                                    <h2><span> <strong>Addresses</strong></span></h2>
                                    <ul class="product_tab_button nav ishover add_address_button" style="margin-right:10px;margin-left:auto;" role="tablist">
                                        <p @click="openAddAddressSidebar"><strong><i class="fa fa-plus"></i>&nbsp;Add Address</strong></p>
                                    </ul>
                                </div>
                                <div class="table" style="overflow-x: auto">
                                    <table class="table">
                                        <thead>
                                            <tr>
                                                <th></th>
                                                <th>Recepient</th>
                                                <th>Shipping Address</th>
                                                <th>Delivery Area</th>
                                                <th></th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            <tr v-for="(data, index) in addresses" :key="index">
                                                <td><p-radio @change="updateDefaultAddress(data)" class="p-default p-round" :checked="data.is_default == 1 ? true : false" name="default-address">
                                                </p-radio></td>
                                                <td><strong>{{ data.recipient_name }}</strong></td>
                                                <td><strong>{{ data.address_title }}</strong><br>{{ data.address_line1 }}</td>
                                                <td>{{ data.city }}, {{ data.state }}, {{ data.country }}<br>{{ data.pincode }}</td>
                                                <td style="font-size: 20px;"><span @click="editAddress(data)" class="lnr lnr-pencil ishover icon-hover"></span><span @click="clickRemoveAddress(data)" class="lnr lnr-trash ml-3 ishover icon-hover"></span></td>
                                            </tr>
                                        </tbody>
                                    </table>
                                    <!-- NODATA STATE -->
                                    <no-data v-if="addresses.length == 0" :image="'/img/nodata/address.jpg'" :title="'noData.oopsYouHaveNoAddress'" :subtitle="'noData.pleaseAddAnAddress'" />
                                </div>
                            </div>
                            <div class="tab-pane fade" id="orders">
                                <div class="section_title col-12 px-0">
                                    <h2><span> <strong>Order &amp; Tracking</strong></span></h2>
                                </div>
                                <div class="table" style="overflow-x: auto">
                                    <table class="table">
                                        <thead>
                                            <tr>
                                                <th>Order ID</th>
                                                <th>Date</th>
                                                <th>Status</th>
                                                <th>Courier</th>
                                                <th>Total</th>
                                                <th></th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            <tr v-for="(data, index) in invoices" :key="index">
                                                <td>{{ data.name }}</td>
                                                <td>{{ data.posting_date | moment('DD MMMM YYYY') }}</td>
                                                <td v-if="data.shipping_status == 'Pending'"><span class="success" v-if="data.paid == 1">Paid</span><span class="success" v-else>Pending</span></td>
                                                <td v-else><span class="success">{{data.shipping_status}}</span></td>
                                                <td>{{ data.courier }} - {{ data.service }}</td>
                                                <td>{{ currency(data.grand_total) }}</td>
                                                <td style="font-size: 20px;"><span class="lnr lnr-eye icon-hover ishover" @click="fetchDetailInvoice(data.name)"></span></td>
                                            </tr>
                                        </tbody>
                                    </table>

                                    <!-- NODATA STATE -->
                                    <no-data v-if="invoices.length <= 0" :image="'/img/nodata/order.jpg'" :title="'noData.youDontHaveOrderYet'" :subtitle="'noData.afterSHoppingYouCanMonitor'" />
                                
                                    <div v-if="isLoadingSalesInvoice">
                                        <loading style="margin-left: auto; margin-right: auto" :animation-duration="1000" :dot-size="15" :dots-num="3" :color="'#e74c34'"/>
                                    </div>

                                    <div class="cart_links_btn mt-4" v-if="!isLoadingSalesInvoice && !isLastSalesInvoice" @click="getSalesInvoice(invoices.length)">
                                        <a class="load_more_link">{{"global.loadMore" | translate}}</a>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
                                    
            <!-- Confirmation Remove Address -->
            <confirmation-modal name="removeAddressModal" :width="$store.state.isMobile ? '80%' : '30%'">
                <template v-slot:title>
                    <div class="text-center">
                        <h4 style="font-size: 20px">{{ "account.addresses.deleteItem" | translate }}</h4>
                    </div>
                </template>

                <template v-slot:subtitle>
                    <div class="text-center my-2">
                        <div v-if="selectedDeletedItem">
                            <h4 class="mb-1"><b>{{ selectedDeletedItem.address_title }} - {{ selectedDeletedItem.recipient_name }}</b></h4>
                            <p class="mb-0 lh-1-2">{{ selectedDeletedItem.address_line1 }}</p>
                            <p class="mb-0 lh-1-2">{{ selectedDeletedItem.city }}, {{ selectedDeletedItem.state }}, {{ selectedDeletedItem.country }}</p>
                            <p class="mb-0 lh-1-2">{{ selectedDeletedItem.pincode }}</p>
                        </div>
                    </div>
                </template>

                <template v-slot:action>
                    <div class="row mx-0 my-2">
                        <div class="col-6 col-md-4 offset-md-2 pr-1 text-center">
                            <button class="btn br-8 btn-block btn-sm btn-outline-danger" @click="$modal.hide('removeAddressModal')"><b>{{ 'Tidak' }}</b></button>
                        </div>
                        <div class="col-6 col-md-4 pl-1 text-center">
                            <!-- <div class="btn-block btn-modal-positive py-2">Ya</div> -->
                            <button class="btn br-8 btn-block btn-sm btn-danger" @click="deleteAddress"><b>{{ 'Ya' }}</b></button>
                        </div>
                    </div>
                </template>

                <template v-slot:background>
                    <img src="/img/modal/delete_address.jpg" style="opacity: 0.05; position: absolute; bottom: 0; right: 0; width: 100%; height: 100%; object-fit: cover"/>
                </template>
            </confirmation-modal>
            <!-- End Confirmation Remove Address -->

            <!-- Confirmation Complaint -->
            <confirmation-modal name="complaintModal" :width="$store.state.isMobile ? '80%' : '30%'">
                <template v-slot:title>
                    <div class="text-center">
                        <h4 style="font-size: 20px">{{ "salesInvoice.confirmationComplaintTitle" | translate }}</h4>
                    </div>
                </template>

                <template v-slot:subtitle>
                    <div class="text-center my-2">
                        <div>
                            <h4 class="mb-1"><b>{{ invoice }}</b></h4>
                            <label for="reason">{{ "salesInvoice.confirmationComplaintSubtitle" | translate }}</label>
                            <textarea class="form-control" v-model="reason" id="" rows="3"></textarea>
                        </div>
                    </div>
                </template>

                <template v-slot:action>
                    <div class="row mx-0 my-2">
                        <div class="col-6 col-md-4 offset-md-2 pr-1 text-center">
                            <button class="btn br-8 btn-block btn-sm btn-outline-danger" @click="$modal.hide('complaintModal'); fetchDetailInvoice(invoice)"><b>{{ 'Cancel' }}</b></button>
                        </div>
                        <div class="col-6 col-md-4 pl-1 text-center">
                            <!-- <div class="btn-block btn-modal-positive py-2">Ya</div> -->
                            <button class="btn br-8 btn-block btn-sm btn-danger" @click="$modal.hide('complaintModal'); complaintInvoice(invoice)"><b>{{ 'Send' }}</b></button>
                        </div>
                    </div>
                </template>

                <template v-slot:background>
                    <img src="/img/modal/delete_address.jpg" style="opacity: 0.05; position: absolute; bottom: 0; right: 0; width: 100%; height: 100%; object-fit: cover"/>
                </template>
            </confirmation-modal>

            <!-- Confirmation Finish -->
            <confirmation-modal name="finishModal" :width="$store.state.isMobile ? '80%' : '30%'">
                <template v-slot:title>
                    <div class="text-center">
                        <h4 style="font-size: 20px">{{ "salesInvoice.confirmationFinishTitle" | translate }}</h4>
                    </div>
                </template>

                <template v-slot:subtitle>
                    <div class="text-center my-2">
                        <div>
                            <h4 class="mb-1"><b>{{ invoice }}</b></h4>
                        </div>
                    </div>
                </template>

                <template v-slot:action>
                    <div class="row mx-0 my-2">
                        <div class="col-6 col-md-4 offset-md-2 pr-1 text-center">
                            <button class="btn br-8 btn-block btn-sm btn-outline-danger" @click="$modal.hide('finishModal'); fetchDetailInvoice(invoice)"><b>{{ 'Tidak' }}</b></button>
                        </div>
                        <div class="col-6 col-md-4 pl-1 text-center">
                            <!-- <div class="btn-block btn-modal-positive py-2">Ya</div> -->
                            <button class="btn br-8 btn-block btn-sm btn-danger" @click="$modal.hide('finishModal'); finishInvoice(invoice)"><b>{{ 'Ya' }}</b></button>
                        </div>
                    </div>
                </template>

                <template v-slot:background>
                    <img src="/img/modal/finish_invoice.jpg" style="opacity: 0.05; position: absolute; bottom: 0; right: 0; width: 100%; height: 100%; object-fit: cover"/>
                </template>
            </confirmation-modal>
        <!--Sidebar Choose Delivery end-->
        </div>
    </section>
    <!-- my account end   -->
  </div>
</template>

<script>
import apiRequest from '@/helper/apiRequest.js'
import currency from '@/helper/currency.js'

export default {
  name: 'Account',
  components: { 
      
   },
  data () {
    return {
        customer: {},
        addresses: [],
        invoices: [],

        selectedDeletedItem: null,
        isLoadingSalesInvoice: false,
        isLastSalesInvoice: false,

        invoice: '',
        reason: ''
    }
  },
  created () {
    this.$store.state.showLoading = false
    this.checkCookies()

    this.getCustomer()
  },
  mounted(){
    if (this.$route.query.anchor && !apiRequest.isGuest()) {
        if (this.$refs[this.$route.query.anchor]) {
            this.$refs[this.$route.query.anchor].click()
        }
    }
  },
  methods: {
    async doLogout () {
      var response = await apiRequest.logout()
        if(response.data != null){
            location.reload()
        }
        else {
            this.$notify({
                group: 'notification',
                type: 'danger',
                title: 'Oops...',
                text: response.error
            });
        }
    },
    async getCustomer () {
        this.$store.state.showLoading = true
        var body = {
            'identifier': 'Get User Detail'
        }
        var response = await apiRequest.requestRead(body)
        if(response.data != null){
            // Get customer data
            if (response.data.length > 0) {
                this.customer = response.data[0]
            }
        }
        else {
            this.$notify({
                group: 'notification',
                type: 'danger',
                title: 'Oops...',
                text: response.error
            });
        }
        this.$store.state.showLoading = false
    },
    async updateCustomer (e) {
        // to prevent form submitted
        e.preventDefault()

        if (this.customer.customer_name == "" || this.customer.mobile_no == "") {
            this.$notify({
                group: 'notification',
                type: 'danger',
                title: 'Oops...',
                text: "account.myProfile.mandatory"
            });
            return
        }

        this.$store.state.showLoading = true
        var body = {
            "mobile_no": this.customer.mobile_no,
            "gender": this.customer.gender,
            "birth_date": this.customer.birth_date,
            "name": this.customer.name,
            "first_name": this.first_name,
            "doctype": "User"
        }
        var response = await apiRequest.updateDoc(body)
        if(response.data == null) {
            this.getCustomer()
            this.$notify({
                group: 'notification',
                type: 'danger',
                title: 'Oops...',
                text: response.error
            });
        } 
        else {
            this.$notify({
                group: 'notification',
                type: 'success',
                title: 'Yey...',
                text: "Success updating account"
            });
        }
        this.$store.state.showLoading = false
        return false
    },

    async getAddress () {
        this.$store.state.showLoading = true
        this.addresses = []
        var body = {
            'identifier': 'Get Address'
        }
        var response = await apiRequest.requestRead(body)
        if(response.data != null){
            // Get customer data
            if (response.data.length > 0) {
                this.addresses = response.data
            }
        }
        else {
            this.$notify({
                group: 'notification',
                type: 'danger',
                title: 'Oops...',
                text: response.error
            });
        }
        this.$store.state.showLoading = false
    },
    async updateAddress (address) {
        this.$store.state.showLoading = true
        var response = await apiRequest.createOrUpdateDoc(address)
        if(response.data == null) {
            this.getAddress()
            this.$notify({
                group: 'notification',
                type: 'danger',
                title: 'Oops...',
                text: response.error
            });
        } else {
            this.$notify({
                group: 'notification',
                type: 'success',
                title: 'Yey...',
                text: 'account.addresses.successChangeDefaultAddress'
            });
        }
        this.$store.state.showLoading = false
    },
    updateDefaultAddress (address) {
        address.is_default = 1
        address.doctype = "Address"

        this.updateAddress(address)
    },
    editAddress (address) {
        this.$store.dispatch('openSideBar', {
            'sidebar': 'addAddress',
            'open': true,
            'data': address,
            'callback': () => {
                this.getAddress()
            }
        })
    },
    async getSalesInvoice (page = 0) {
        this.$store.state.showLoading = true
        this.isLoadingSalesInvoice = true

        if (page == 0) {
            this.invoices = []
        }

        var body = {
            'identifier': 'Get Sales Invoice List',
            'page': page
        }
        var response = await apiRequest.requestRead(body)
        if(response.data != null){
            // Get customer data
            if (response.data.length > 0) {
                response.data.forEach(el => {
                    this.invoices.push(el)
                });
            }

            if (response.data.length < 20) {
                this.isLastSalesInvoice = true
            }
        }
        else {
            this.$notify({
                group: 'notification',
                type: 'danger',
                title: 'Oops...',
                text: response.error
            });
        }
        this.isLoadingSalesInvoice = false
        this.$store.state.showLoading = false
    },
    async fetchDetailInvoice(name){
        this.$store.state.showLoading = true
        var body = {
            'identifier': 'Get Detail Sales Invoice',
            'name': name
        }
        var response = await apiRequest.requestRead(body)
        if(response.data != null){
            // Get customer data
            if (response.data.length > 0) {
                this.$store.dispatch('openSideBar', {
                    'sidebar': 'salesInvoice',
                    'open': true,
                    'callback': this.getSalesInvoice,
                    'events': {
                        'didClickFinish': (invoice) => {
                            this.invoice = invoice
                            this.$modal.show('finishModal')
                        },
                        'didClickComplaint': (invoice) => {
                            this.invoice = invoice
                            this.$modal.show('complaintModal')
                        }
                    }
                })
                this.$store.state.invoice = response.data[0]
            }
        }
        else {
            this.$notify({
                group: 'notification',
                type: 'danger',
                title: 'Oops...',
                text: response.error
            });
        }
        this.$store.state.showLoading = false
    },
    async finishInvoice (invoice) {
        this.$store.state.showLoading = true
        var body = {
            "name" : invoice,
            "shipping_status" : "Completed"
        }
        var response = await apiRequest.changeInvoiceStatus(body)
        if(response.data != null){
            // Get customer data
            this.$notify({
                group: 'notification',
                type: 'success',
                title: 'Success',
                text: "salesInvoice.notifFinish"
            });
            this.getSalesInvoice()
        }
        else {
            this.$store.state.sidebar.salesInvoice = true
            this.$notify({
                group: 'notification',
                type: 'danger',
                title: 'Oops...',
                text: response.error
            });
        }
    },
    async complaintInvoice (data) {
        this.$store.state.showLoading = true
        var body = {
            "name" : data,
            "shipping_status" : "Payment Expired",
            "reason": this.reason
        }
        var response = await apiRequest.changeInvoiceStatus(body)
        if(response.data != null){
            // Get customer data
            this.$notify({
                group: 'notification',
                type: 'success',
                title: 'Success',
                text: "salesInvoice.notifComplaint"
            });
            this.getSalesInvoice()
        }
        else {
            this.$store.state.sidebar.salesInvoice = true
            this.$notify({
                group: 'notification',
                type: 'danger',
                title: 'Oops...',
                text: response.error
            });
        }
        this.reason = ''
        this.$store.state.showLoading = false
    },
    checkCookies() {
        if (apiRequest.isGuest()) {
            location.href = '/'
        }
    },

    currency (amount) {
        return currency.format("IDR", amount)
    },

    openAddAddressSidebar () {
        this.$store.dispatch('openSideBar', {
            'sidebar': 'addAddress',
            'open': true,
            'callback': () => {
                this.getAddress()
            }
        })
    },
    clickRemoveAddress (data) {
        this.selectedDeletedItem = data
        console.log(this.$modal)
        this.$modal.show('removeAddressModal')
    },
    async deleteAddress() {
        this.$modal.hide('removeAddressModal')
        this.$store.state.showLoading = true
        var body = {
            "name": this.selectedDeletedItem.name,
            "enabled": 0,
            "doctype": "Address"
        }

        var response = await apiRequest.createOrUpdateDoc(body)
        if(response.data == null) {
            this.getAddress()
            this.$notify({
                group: 'notification',
                type: 'danger',
                title: 'Oops...',
                text: response.error
            });
        } else {
            this.$notify({
                group: 'notification',
                type: 'success',
                title: 'Yey...',
                text: 'account.addresses.successDeleteAddress'
            });
            this.getAddress()
        }
        this.$store.state.showLoading = false
    }
  }
}
</script>
