<template>
  <div>
    <!-- customer login start -->
    <div class="customer_login mt-32">
        <div class="container">
            <div class="row">
                <!--reset password start-->
                <div class="col-lg-6 col-md-6 col-12">
                    <div class="account_form sticky-left">
                        <h2>Forgot Password</h2>
                        <form>
                            <p>
                                <label>Email <span>*</span></label>
                                <input v-model="reset.email" type="email">
                            </p>
                            <div class="login_submit">
                                <div class="auth_button" @click="resetPwd">Submit</div>
                            </div>
                        </form>
                    </div>
                </div>
                <!--reset password start-->
            </div>
        </div>
    </div>
    <!-- customer login end -->
  </div>
</template>

<script>
import apiRequest from '@/helper/apiRequest.js'

export default {
  name: 'Account',
  components: { 
      
   },
  data () {
    return {
        reset: {
            email: ''
        }
    }
  },
  created () {
        this.$store.state.showLoading = false
  },
  mounted(){
  },
  methods: {
    async resetPwd() {
        if (this.reset.email == "") {
            this.$notify({
                group: 'notification',
                type: 'danger',
                title: 'Oops...',
                text: 'Silahkan isi email'
            });
            return
        }
        this.$store.state.showLoading = true
        var body = {
            'email_id': this.reset.email
        }
        var response = await apiRequest.forgetPwd(body)
        if(response.data != null){
            this.$notify({
                group: 'notification',
                type: 'success',
                title: 'Yey...',
                text: 'Jika akun anda terdaftar sebagai Wijaya Superstore Account, kami mengirimkan email untuk instruksi pembaruan kata sandi.'
            });
            this.$router.push('/auth')
        }
        else {
            this.$notify({
                group: 'notification',
                type: 'danger',
                title: 'Oops...',
                text: response.error
            });
        }
        this.$store.state.showLoading = false
    },
  }
}
</script>
