<template>
  <div>
      <!--blog area start-->
    <div>
        <div class="container">
            <div class="row">
                
                <div class="section_title col-12 mt-4">
                    <h2><span> <strong>Blog</strong></span></h2>
                </div>
                <div class="col-12 mb-5">
                    <div class="blog_wrapper">
                        <div class="single_blog row" v-for="(data, index) in dataBlog" :key="index">
                            <div class="col-lg-4 col-md-5 col-sm-12">
                                <img :src="$store.state.baseURL + encodeURIComponent(data.image)" alt="" style="width: 100%; height: 250px;" class="card">
                            </div>
                            <div class="blog_content col-lg-8 col-md-7 col-sm-12 pl-3">
                                <h3 class="mt-2"><b>{{ data.title || ''}}</b></h3>
                                <div class="blog_meta">
                                    <span class="post_date"><i class="fa-calendar fa mr-1"></i>  {{ data.published_on | moment('DD MMMM YYYY') }}</span>
                                    <span class="author"><i class="fa fa-user-circle mr-1"></i>  {{ 'blog.postby' | translate }} : {{ data.modified_by || '' }}</span>
                                    <span class="category"><i class="fa fa-folder-open mr-1"></i>  {{data.blog_category || ''}}</span>
                                </div>
                                <div class="blog_desc">
                                    <p>{{ data.blog_intro || ''}} </p>
                                </div>
                                <div class="readmore_button">
                                    <router-link :to="'/blog/'+data.name">{{ 'blog.readMore' | translate }}</router-link>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
    <!--blog area end-->
  </div>
</template>

<script>
import apiRequest from '@/helper/apiRequest.js'

export default {
  name: 'Blog',
  components: { 
      
    },
  data () {
    return {
      page: 0,
      dataBlog: []
    }
  },
  created () {
      this.fetchBlog();
  },
  methods: {
    async fetchBlog() {
        this.$store.state.showLoading = true
        var body = {
            "identifier" : "Get Blog Post",
            "limit": 9999
        }
        var response = await apiRequest.requestRead(body)
        if(response.data != null){
            if(response.data.length > 0){
                for(let i in response.data){
                    this.dataBlog.push(response.data[i])
                }
            }
        }
        else {
            this.$notify({
                group: 'notification',
                type: 'danger',
                title: 'Oops...',
                text: response.error
            });
        }
        this.$store.state.showLoading = false
        console.log("Blog",this.dataBlog)
    },
  }
}
</script>
