<template>
  <div style="margin-bottom: -10px;">
        <div class="container" v-if="isValidUrl">
            <div class="row my-5 mx-2">
                <div class="col-lg-8 offset-lg-2 col-12 px-0">
                    <div class="section_title mb-2">
                        <h2><span>{{ salesInvoice }}</span></h2>
                    </div>
                    <h5>{{ salesInvoiceDate }}</h5>
                </div>
                <div class="col-lg-8 offset-lg-2 col-12">
                    <div v-for="(item, index) in data" :key="'item_' + index" class="card py-2 row my-3" style="flex-direction: row">
                        <div class="col-lg-2 col-md-3 col-sm-3 mb-3">
                            <img v-if="item.image" :src="$store.state.baseURL + item.image" width="100%" class="card"/>
                            <img v-else src="@/assets/noimage.jpg" width="100%" class="card"/>
                        </div>
                        <div class="col-lg-9 col-md-8 col-sm-8">
                            <div class="row">
                                <div class="col-12">
                                    <h4><strong>{{item.item_name || ''}}</strong></h4>
                                    <div class="price_box">
                                        <span class="current_price">{{ getCurrency(item.amount) }}</span>
                                    </div>
                                    
                                    <!-- Rating read only, untuk item yang sudah dirating -->
                                    <star-rating
                                        v-if="item.rating > 0"
                                        class="mt-2 mb-2"
                                        :show-rating="false"
                                        :rating="item.rating"
                                        :read-only="true"
                                        inactive-color="#e9e9e9"
                                        active-color="#dc3545"
                                        :star-size="20"></star-rating>

                                    <!-- Untuk item yang belum dirating -->
                                    <star-rating
                                        v-else
                                        class="mt-2 mb-2"
                                        :show-rating="false"
                                        v-model="item.did_rating"
                                        :read-only="false"
                                        @rating-selected="rate($event, item.name)"
                                        inactive-color="#e9e9e9"
                                        active-color="#dc3545"
                                        :star-size="20"></star-rating>
                                    
                                    <transition name="slide">
                                        <div v-if="item.rating > 0">
                                            <p class="mb-1">{{ item.feedback }}</p>
                                            <p style="font-size: 11px"><em>{{ item.rating_date | moment('DD MMMM YYYY, H:m:s') }}</em></p>
                                        </div>
                                    </transition>

                                    <transition name="slide">
                                        <div v-if="item.did_rating" class="product_review_form">
                                            <form>
                                                <div class="row">
                                                    <div class="col-12">
                                                        <label for="review_comment">{{ "rating.yourReview" | translate }} </label>
                                                        <textarea class="py-2" name="comment" id="review_comment" v-model="item.review"></textarea>
                                                    </div>
                                                </div>
                                                <button type="button" @click="didRating(item)">{{ "rating.submit" | translate }}</button>
                                            </form>
                                        </div>
                                    </transition>
                                </div>
                            </div>
                        </div>
                        <!-- <div class="col-12 text-right mt-2">
                            <div :style="index < data.length - 1 ? 'display: block' : 'display: none'" class="line"></div>
                        </div> -->
                    </div>
                </div>
            </div>
        </div>
        <!--error section area start-->
        <div class="error_section" v-else>
            <div class="container">
                <div class="error_form">
                    <!-- <h1>404</h1> -->
                    <img src="img/nodata/404.jpg" width="100%" style="max-width: 400px;">
                    <h2 class="mt-4"><b>{{ 'rating.salesInvoiceNotValid' | translate }}</b></h2>
                    <p>{{ "rating.salesInvoiceNotValidSubtitle" | translate }}</p>
                </div>
            </div>
        </div>
        <!--error section area end-->
  </div>
</template>

<style>

</style>

<script>
import apiRequest from '@/helper/apiRequest.js'
import currency from '@/helper/currency.js'
import Vue from 'vue'

export default {
  name: 'Rating',
  data () {
    return {
        url: '',
        isValidUrl: false,

        salesInvoice: '',
        data: []
    }
  },
  created () {
    this.$store.state.showLoading = false
    this.salesInvoice = decodeURIComponent(this.$route.params.si)

    this.fetch()
  },
  computed: {
    salesInvoiceDate () {
      if (this.data.length > 0) {
        return Vue.moment(this.data[0].posting_date).format('DD MMMM YYYY')
      }
      return ''
    }
  },
  methods: {
    async fetch () {
        this.$store.state.showLoading = true
        var body = {
            'identifier': 'GET Sales Invoice Rating Item',
            'sales_invoice': this.salesInvoice
        }
        var response = await apiRequest.requestRead(body)
        if(response.data != null){
            // Get customer data
            if (response.data.length > 0) {
                this.isValidUrl = true
                this.data = response.data
            }
        }
        else {
            this.$notify({
                group: 'notification',
                type: 'danger',
                title: 'Oops...',
                text: response.error
            });
        }
        this.$store.state.showLoading = false
    },
    async didRating (item) {
        this.$store.state.showLoading = true
        var body = {
            "item": item.item_code,
            "rating": item.did_rating,
            "feedback": item.review,
            "sales_invoice": item.parent,
            "customer": item.customer,
            "doctype": "Rating"
        }
        var response = await apiRequest.createDoc(body)
        if(response.data != null){
            this.$notify({
                group: 'notification',
                type: 'success',
                title: 'Success',
                text: "Success review product"
            })
            this.fetch()    
        }
        else {
            this.$store.state.showLoading = false
            this.$notify({
                group: 'notification',
                type: 'danger',
                title: 'Oops...',
                text: response.error
            });
        }
    },
    rate (rating, name) {
        this.resetOtherRating(name)
    },
    resetOtherRating(name) {
        this.data.forEach(el => {
            if (el.name != name) {
                el.did_rating = null
            }
        })
    },
    getCurrency(money) {
        return currency.format('IDR', money)
    },
  }
}
</script>
