<template>
    <div class="single_product">
        <div class="product_name">
            <h3><a href="product-details.html" class="text-ellipsis">{{ data.title || 'Product Name' }}</a></h3>
            <p class="manufacture_product"><a href="#">{{ data.category || 'Product Category' }}</a></p>
        </div>
        <div class="product_thumb">
            <a class="primary_img" href="product-details.html">
                <img :src="$store.state.baseURL + encodeURIComponent(data.image)" alt="" id="productImage" :style="imageHeight > 0 ? 'height: ' + imageHeight + 'px' : ''">
            </a>
            <!-- <a class="secondary_img" href="product-details.html"><img :src="data.image" alt=""></a> -->
            <div class="label_product" v-show="data.discount > 0">
                <span class="label_sale">-57%</span>
            </div>

            <div class="action_links">
                <ul>
                    <li class="quick_button"><a href="#" data-toggle="modal" data-target="#modal_box" title="quick view"> <span class="lnr lnr-magnifier"></span></a></li>
                    <li class="wishlist"><a href="wishlist.html" title="Add to Wishlist"><span class="lnr lnr-heart"></span></a></li>
                    <li class="compare"><a href="compare.html" title="compare"><span class="lnr lnr-sync"></span></a></li>
                </ul>
            </div>
        </div>
        <div class="product_content">
            <div class="product_ratings">
                <ul>
                    <li v-for="(star, index) in data.rating" :key="'rating' + index"><a href="#"><i class="ion-star"></i></a></li>
                </ul>
            </div>
            <div class="product_footer d-flex align-items-center">
                <div class="price_box" v-if="data.old_price == 0 || data.old_price === undefined || data.old_price === null">
                    <span class="regular_price">Rp.{{ data.current_price }}</span>
                </div>
                <div class="price_box" v-else>
                    <span class="current_price">${{ data.current_price }}</span>
                    <span class="old_price">Rp.{{ data.old_price }}</span>
                </div>
                <div class="add_to_cart">
                    <a href="cart.html" title="add to cart"><span class="lnr lnr-cart"></span></a>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import helper from '@/helper/global.js'

export default {
  props: {
    data: {},
    /*  Format
        data: {
            title: String,
            category: String,
            discount: Int,
            rating: Int,
            old_price: Int,
            current_price: Int,
            image: String
        }
    */
  },
  data () {
    return {
      imageHeight: 0
    }
  },
  created () {
    window.addEventListener('resize', this.resize)
  },
  mounted () {
    this.resize()
  },
  methods: {
    resize() {
      this.imageHeight = helper.resizeElement('productImage', 1)
    }
  }
}
</script>