                                    <a @click="addToSchoppingCart(false)" :class="$store.state.isMobile ? 'btn-sm' : ''" class="btn btn-danger ml-2 my-1" :minimum="1"><b>{{"product.addToCart" | translate}}</b></a>
                                </div>
                                <div class="col-8" v-else>
                                    <a @click="addToSchoppingCart(true)" :class="$store.state.isMobile ? 'btn-sm' : ''" class="btn btn-outline-secondary disable-btn disabled ml-2 my-1" :minimum="1"><b>{{"product.buyNow" | translate}}</b></a>
                                    <a @click="addToSchoppingCart(false)" :class="$store.state.isMobile ? 'btn-sm' : ''" class="btn btn-secondary disabled disable-btn ml-2 my-1" :minimum="1"><b>{{"product.addToCart" | translate}}</b></a>
