<script>
import carousel from 'vue-owl-carousel2'
import apiRequest from '@/helper/apiRequest.js'
import currency from '@/helper/currency.js'
import Vue from 'vue'
import global from '@/helper/global.js'

export default {
    name: 'ProductDetail',
    data () {
      return {
        uomSelected: 0,
        variantSelected : 0,
        variantData: [],
        uomData: [],
        imageList: [],
        mainImage: '',
        variantString: '',
        qty: 1,
        maxQty: 0.0,
        stockUom: "",
        salesUom: "",
        multiplicationQty: 1.0,
        thumbnail: {
            width: 500,
            height: 500
        },
        zoomKey: 0,
        listImageVariant: [],
        listPhotoChanged: 0,
        isLiked: false,
        flashSaleData: null,
        priceAfterDiscount: 0.0,

        // Rating / Review
        countRating: 0,
        avgRating: 0,
        rating: [],
        isSeeMoreRating: true,

        multiplication_qty: 1,
        minimal_qty: 1
      }
    },
    components: { carousel },
    created () {
        window.addEventListener('resize', this.resizeThumbnail)
        this.fetchData()
        this.fetchItemIsLiked()
        setTimeout(() => {
            this.resizeThumbnail()
        }, 1000);
    },
    mounted () {
        this.resizeThumbnail()
    },
    methods: {
        flashSaleCountDown(date){
            return Vue.moment(date).format('MMM DD, YYYY hh:mm:ss')
        },
        resizeThumbnail () {
            setTimeout(() => {
                var width = this.$refs.thumbnail_col.clientWidth - 30
                var height = width

                this.thumbnail.width = parseInt(width) - 2
                this.thumbnail.height = parseInt(height) - 2
            }, 500)
        },
        chooseVariant (index) {
            this.variantSelected = index
            this.mainImage = this.variantData[index].image
            this.fetchDetailVariant()
            this.fetchReview()
        },
        chooseUOM (index) {
            this.uomSelected = index
            this.fetchDetailVariant()
        },
        async fetchData() {
            this.$store.state.showLoading = true
            var body = {
                "identifier" : "Get Item Variant",
                "item": this.$route.params.id
            }
            var response = await apiRequest.requestRead(body)
            console.log("GET ITEM VAR", response)
            if(response.data != null){
                for (let i in response.data){
                    var data = {
                        "name": response.data[i].name,
                        "image": response.data[i].image,
                        "description": response.data[i].description,
                        "item_name": response.data[i].item_name,
                        "item_group": response.data[i].item_group,
                        "attribut_value": response.data[i].item[0].attributes[0].attribute_value,
                        "item_price": response.data[i].item_price,
                        "item_code": response.data[i].item_code,
                        "isSelected": false,
                        "weight_uom": response.data[i].weight_uom,
                        "weight_per_unit": response.data[i].weight_per_unit
                    }
                    this.variantData.push(data)

                    if (i == 0){
                        this.chooseVariant(0)
                        this.variantString += response.data[i].item[0].attributes[0].attribute_value
                    }
                    else {
                        this.variantString += ', ' + response.data[i].item[0].attributes[0].attribute_value
                    }
                }
            }
            else {
                this.$notify({
                    group: 'notification',
                    type: 'danger',
                    title: 'Oops...',
                    text: response.error
                });
            }
            this.$store.state.showLoading = false
        },
        async fetchItemIsLiked () {
            this.$store.state.showLoading = true
            var body = {
                "identifier" : "Get Item Is Liked",
                "item": this.$route.params.id,
            }
            var response = await apiRequest.requestRead(body)

            if (response.data) {
                this.isLiked = response.data.length > 0
            }
            this.$store.state.showLoading = false
        },
        async fetchReview (page = 0) {
            this.$store.state.showLoading = true

            var body = {
                "identifier" : "Get Item Rating",
                "item": this.variantData[this.variantSelected].item_code,
                "page": page
            }
            var response = await apiRequest.requestRead(body)

            if (response.data) {
                if (page == 0) {
                    this.rating = response.data
                } else {
                    response.data.forEach(el => {
                        this.rating.push(el)
                    })
                }

                if (response.data.length < 20) {
                    this.isSeeMoreRating = false
                } else {
                    this.isSeeMoreRating = true
                }
            } else {
                this.$notify({
                    group: 'notification',
                    type: 'danger',
                    title: 'Oops...',
                    text: response.error
                });
            }

            this.$store.state.showLoading = false
        },
        seeMoreRating () {
            this.fetchReview(this.rating.length)
        },
        async fetchDetailVariant() {
            this.$store.state.showLoading = true
            this.maxQty = 0.0
            this.listImageVariant = []
            this.flashSaleData = null
            this.priceAfterDiscount = 0.0
           

            var body = {
                "identifier" : "Get Item Details",
                "item": this.variantData[this.variantSelected].item_code,
                "customer": "API WSS",
                "transaction_date": this.$moment(Date()).format('YYYY-MM-DD'),
                "uom": uom
            }
            var response = await apiRequest.requestRead(body)

            if(response.data != null){
                this.stockUom = response.data.item.stock_uom
                this.salesUom = response.data.item.stock_uom
                
                if (response.data.item.sales_uom != undefined && response.data.item.sales_uom != "") {
                    this.salesUom = response.data.item.sales_uom
                }
                
                // alert(this.response.data.item_detail.price_list_rate)
                
                var uoms = response.data.item.uoms
                var uomData = []
                var defaultUOM = {}
                this.uomData = []
                if (uoms.length > 0) {
                    for (var i=0;i<uoms.length;i++) {
                        if (uoms[i].uom == this.salesUom) {
                            defaultUOM = uoms[i]
                        } else {
                            uomData.push(uoms[i])
                        }
                    }
                }
                this.uomData.push(defaultUOM)
                for (var j=0;j<uomData.length;j++) {
                    this.uomData.push(uomData[j])
                }

                this.variantData[this.variantSelected].item_price = response.data.item_detail.price_list_rate

                // multiplication features
                this.qty = this.multiplication_qty || 1
                this.multiplicationQty = Math.max(response.data.item.multiplication_qty,1.0)
                console.log('item detail',response.data)
                this.maxQty = response.data.item_detail.actual_qty
                console.log("Max: ", response.data.item_detail.actual_qty)
                this.$store.state.showLoading = false
                console.log("FS", response.data.item.flash_sale)
                if (response.data.item.flash_sale != null) {
                    this.fetchFlashSale()
                }
                this.countRating = response.data.item.total_rating
                this.avgRating = response.data.item.rating

                
                //uom features
                var uom = ""
                if (this.uomData.length > 0) {
                    uom = this.uomData[this.uomSelected].uom
                    console.log("UOM",this.uomData[this.uomSelected].minimal_qty)
                    console.log("UOM",this.uomData[this.uomSelected].multiplication_qty)
                    this.multiplication_qty = this.uomData[this.uomSelected].multiplication_qty
                    this.minimal_qty = this.uomData[this.uomSelected].minimal_qty
                }
            }
            else {
                this.$notify({
                    group: 'notification',
                    type: 'danger',
                    title: 'Oops...',
                    text: response.error
                });
                this.$store.state.showLoading = false
            }
            this.fetchVariantPicture()
            this.insertSeenItem()
        },

        async insertSeenItem() {
            var body = {
                "reference_doctype": "Item",
                "reference_name": this.variantData[this.variantSelected].item_code,
                "doctype": "Log Document View"
            }
            await apiRequest.createDoc(body)
        },

        async fetchFlashSale() {
            this.$store.state.showLoading = true
            var body = {
                "identifier" : "Get Flash Sale by Item",
                "item": this.variantData[this.variantSelected].item_code
            }
            var response = await apiRequest.requestRead(body)
            console.log("FS DATA", response)
            if(response.data != null){
                if(response.data.length > 0){
                    this.flashSaleData = response.data[0]
                    this.priceAfterDiscount = global.calculateDiscount(this.flashSaleData, this.variantData[this.variantSelected].item_price)
                    this.flashSaleData.start = this.flashSaleCountDown(this.flashSaleData.valid_from)
                    this.flashSaleData.end = this.flashSaleCountDown(this.flashSaleData.valid_till)
                    this.flashSaleData.now = this.flashSaleCountDown(this.flashSaleData.now)
                    // //COUNT DISC
                    // var value = this.flashSaleData.discount_percentage_or_amount
                    // var price = this.variantData[this.variantSelected].item_price
                    // var maxDisc = this.flashSaleData.maximal_discount
                    // if (this.flashSaleData.discount_type == "Discount Percentage") {
                    //     var disc = price * value / 100.0
                    //     console.log("DISC", disc)
                    //     if (disc > maxDisc && maxDisc != 0.0) {
                    //         this.priceAfterDiscount = price - this.flashSaleData.maximal_discount
                    //     }
                    //     else {
                    //         this.priceAfterDiscount = price - disc
                    //     }
                    // }
                    // else if (this.flashSaleData.discount_type == "Discount Amount") {
                    //     this.priceAfterDiscount = price - value
                    // }
                    // else {
                    //     this.priceAfterDiscount = 0.0
                    // }
                }
            }
            else {
                this.$notify({
                    group: 'notification',
                    type: 'danger',
                    title: 'Oops...',
                    text: response.error
                });
            }
            this.$store.state.showLoading = false
        },
        async fetchVariantPicture() {
            var body = {
                "identifier" : "GET Files",
                "attached_to_doctype": "Item",
                "attached_to_name": this.variantData[this.variantSelected].item_code,
            }
            var response = await apiRequest.requestRead(body)
            if(response.data != null){
                this.listImageVariant = response.data
                this.listPhotoChanged ++
            }
            else {
                this.$notify({
                    group: 'notification',
                    type: 'danger',
                    title: 'Oops...',
                    text: response.error
                });
            }
        },
        getCurrency(money) {
            return currency.format('IDR', money)
        },
        async toggleLike(like) {
            this.$store.state.showLoading = true
            let resp = await apiRequest.likeItem(this, this.$route.params.id, like)
            if (resp) {
                this.fetchItemIsLiked()
            }
            this.$store.state.showLoading = false
        },
        async addToSchoppingCart(goToCart) {
            if (apiRequest.isGuest()) {
                this.$router.push({ name: 'auth'})
            }
            else {
                this.$store.state.showLoading = true
                var body = {
                    "parenttype": "Shopping Cart",
                    "item": this.variantData[this.variantSelected].item_code,
                    "parent": this.$store.state.email,
                    "parentfield": "shopping_cart_item",
                    "weight_per_unit": this.variantData[this.variantSelected].weight_per_unit,
                    "doctype": "Shopping Cart Item",
                    "qty": this.qty,
                    "uom": this.uomData[this.uomSelected].uom 
                }
                console.log("Body Add Shopping Cart", body)
                var response = await apiRequest.createOrUpdateDoc(body)
                if(response.data != null){
                    apiRequest.getCartBadge()
                    if(goToCart){
                        this.$router.push({ name: 'cart'})
                    }
                    else {
                        this.$notify({
                        group: 'notification',
                        type: 'success',
                        title: 'Yey...',
                        text: 'notification.successfullyAddedItemToCart'
                    });
                    }
                }
                else {
                    this.$notify({
                        group: 'notification',
                        type: 'danger',
                        title: 'Oops...',
                        text: response.error
                    });
                }
                this.$store.state.showLoading = false
            }
        }
    }
}
</script>