<template>
    <div>
        <!--shop area start-->
        <div class="shop_area">
            <div class="container">
                <div class="row">
                    <div class="col-lg-12 col-md-12">
                        <!--shop wrapper start-->
                        <!--shop toolbar start-->
                        <!-- <div class="shop_title">
                            <h1>shop</h1>
                        </div> -->
                        <div class="row">
                            <div class="col-12">
                                <div class="section_title">
                                    <h2><span> <strong>Wishlist</strong></span></h2>
                                </div>
                            </div>
                        </div>
                        <!--shop toolbar end-->

                        <div class="row shop_wrapper grid_4 mx-0" :key="productKey">
                            <div
                                class="col-md-4 col-sm-6 col-lg-2 p-1"
                                v-for="(data,index) in dataProduct"
                                :key="index">
                                <single-product-tokopedia
                                    :data="{ 
                                        'title': data.item_name,
                                        'category': data.item_group,
                                        'old_price': 0,
                                        'rating': 0,
                                        'discount': 0,
                                        'image': data.image,
                                        'current_price': getCurrency(data.item_price),
                                        'id': data.name
                                    }"
                                    not-wishlist>
                                </single-product-tokopedia>
                            </div>
                        </div>

                        <div class="center" v-if="isLoadingItem">
                            <loading :animation-duration="1000" :dot-size="15" :dots-num="3" :color="'#e74c34'"/>
                        </div>

                        <div class="cart_links_btn mt-4" v-if="!isLoadingItem && !isLastPage" @click="loadMore()">
                            <a>Load More</a>
                        </div>

                        <!-- <div class="shop_toolbar t_bottom">
                            <div class="pagination">
                                <ul>
                                    <li class="current">1</li>
                                    <li><a href="#">2</a></li>
                                    <li><a href="#">3</a></li>
                                    <li class="next"><a href="#">next</a></li>
                                    <li><a href="#">>></a></li>
                                </ul>
                            </div>
                        </div> -->
                        <!--shop toolbar end-->
                        <!--shop wrapper end-->
                    </div>
                </div>
            </div>
        </div>
        <!--shop  area end-->
        <!-- NODATA STATE -->
        <no-data v-if="dataProduct.length == 0" :image="'/img/nodata/wishlist.jpg'" :title="'noData.noWishlist'" :subtitle="'noData.noWishlistSubtitle'" />
                        
    </div>
</template>

<script>
import apiRequest from '@/helper/apiRequest.js'
import currency from '@/helper/currency.js'

export default {
  name: 'Shop',
  data () {
    return {
      dataProduct: [],
      dataBrand: [],
      page : 0,
      isLoadingItem: false,
      isLastPage: false,
      productKey: 0
    }
  },
  components: {
      
  },
  computed: {
  },
  created () {
      this.$store.state.showLoading = false
      if (apiRequest.isGuest()){
          location.href = '/#/auth'
      }
      else {
        this.fetchBrand()
        this.fetchItem(this.page)
      }
  },
  methods: {
    async fetchItem(page) {
        this.isLoadingItem = true
        var body = {
            "identifier" : "Get Liked Item",
            "page" : page,
        }
        var response = await apiRequest.requestRead(body)
        if(response.data != null){
            if (response.data.length == 0 || (response.data.length < 20 && page == 0)) {
                this.isLastPage = true
            }
            if (page == 0) {
                this.dataProduct = []
            }
            for (let i in response.data){ 
                var data = response.data[i]
                this.dataProduct.push(data)
            }
        } else {
            this.$notify({
                group: 'notification',
                type: 'danger',
                title: 'Oops...',
                text: response.error
            });
        }
        this.isLoadingItem = false
        this.productKey += 1
    },
    async fetchBrand() {
        var body = {
            "identifier" : "Get Brand",
            "page" : 0,
            "limit" : 999
        }
        var response = await apiRequest.requestRead(body)
        if(response.data != null){
            this.dataBrand = []
            for (let i in response.data){
                var data = {
                    "name": response.data[i].name,
                    "brand": response.data[i].brand,
                    "isSelected": false,
                }
                this.dataBrand.push(data)
            }
        }
    },
    changeProduct(data){
        if(data == "New Arrival"){
            this.fetchItem("1","%")
        }
        else if(data == "Popular"){
            this.fetchItem("%","1")
        }
        else{
            this.fetchItem("1","%")
        }
    },
    getCurrency(money) {
        return currency.format('IDR', money)
    },
    loadMore(){
        this.page += 20
        this.fetchItem(this.page)
    }
  }
}
</script>
