import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from './components/HelloWorld.vue'
import Home from './screen/home/home.vue'
import Shop from './screen/shop/shop.vue'
import NotFound from './screen/other/404.vue'
import Blog from './screen/blog/blog.vue'
import DetailBlog from './screen/blog/detailBlog.vue'
import ProductDetail from './screen/shop/detail.vue'
import Cart from './screen/cart/cart.vue'
import Checkout from './screen/cart/checkout.vue'
import Payment from './screen/cart/payment.vue'
import Account from './screen/account/account.vue'
import Wishlist from './screen/wishlist/wishlist.vue'
import Auth from './screen/account/auth.vue'
import AboutUs from './screen/about/about.vue'
import ContactUs from './screen/contact/contact.vue'
import Privacy from './screen/other/privacy.vue'
import Tnc from './screen/other/tnc.vue'
import Refund from './screen/other/refundpolicy.vue'
import Rating from './screen/account/rating.vue'
import ResetPassword from './screen/account/resetPassword.vue'
import ForgotPassword from './screen/account/forgotPassword.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/shop/:search',
      name: 'shopSearch',
      component: Shop
    },
    {
      path: '/shop',
      name: 'shop',
      component: Shop
    },
    {
      path: '/product/:id',
      name: 'shopDetail',
      component: ProductDetail
    },
    {
      path: '/blog',
      name: 'blog',
      component: Blog
    },
    {
      path: '/blog/:id',
      name: 'blog-detail',
      component: DetailBlog
    },
    {
      path: '/cart',
      name: 'cart',
      component: Cart
    },
    {
      path: '/payment',
      name: 'payment',
      component: Payment
    },
    {
      path: '/cart/checkout',
      name: 'checkout',
      component: Checkout
    },
    {
      path: '/account',
      name: 'account',
      component: Account
    },
    {
      path: '/wishlist',
      name: 'wishlist',
      component: Wishlist
    },
    {
      path: '/about',
      name: 'about',
      component: AboutUs
    },
    {
      path: '/contact',
      name: 'contact',
      component: ContactUs
    },
    {
      path: '/auth',
      name: 'auth',
      component: Auth
    },
    {
      path: '/privacypolicy',
      name: 'privacypolicy',
      component: Privacy
    },
    {
      path: '/tnc',
      name: 'tnc',
      component: Tnc
    },
    {
      path: '/refundpolicy',
      name: 'refundpolicy',
      component: Refund
    },
    {
      path: '/rating/:si',
      name: 'rating',
      component: Rating
    },
    {
      path: '/reset/:id',
      name: 'reset',
      component: ResetPassword
    },
    {
      path: '/forgot',
      name: 'forgot',
      component: ForgotPassword
    },
    {
      path: '*',
      name: '404',
      component: NotFound
    }
  ],
})
