<template>
  <router-link :to="'/product/'+data.item_code">
      <div class="single_product_tokopedia ishover">
          <div class="product_thumb">
              <a class="primary_img">
                  <img src="@/assets/noimage.jpg" width="100%" alt="" id="productImageFlash" :style="imageHeight > 0 ? 'height: ' + imageHeight + 'px' : ''">
                  <img class="stack-image" :src="data.image != null && data.image != '' && data.image != undefined ? ($store.state.baseURL + encodeURIComponent(data.image)) : '@/assets/noimage.jpg'" width="100%" alt="" id="productImageFlash" :style="imageHeight > 0 ? 'height: ' + imageHeight + 'px' : ''">
              </a>
              <!-- <a class="secondary_img" href="product-details.html"><img :src="data.image" alt=""></a> -->
              <div class="label_product" v-show="data.discount > 0">
                  <span class="label_sale">-57%</span>
              </div>
          </div>
          <div class="product_name_tokopedia">
              <h2><strong><a href="product-details.html" class="text-ellipsis-2">{{ data.title || 'Product Name' }}</a></strong></h2>
              <p class="manufacture_product"><a href="#" class="text-ellipsis">{{ data.category || 'Product Category' }}</a></p>
          </div>
          <div class="product_content">
              <div class="product_ratings">
                  <ul>
                      <li v-for="(star, index) in data.rating" :key="'rating' + index"><a href="#"><i class="ion-star"></i></a></li>
                  </ul>
              </div>
              <div class="product_footer d-flex align-items-center">
                  <div class="price_box">
                      <div class="old_price" style="font-size: 16px" :style="data.old_price === 0 ? 'visibility: hidden' : ''">{{ data.old_price }}</div>
                      <div class="current_price" style="font-size: 28px">{{ data.current_price }}</div>
                  </div>
                  <!-- <div class="add_to_cart">
                      <a href="cart.html" title="add to cart"><span class="lnr lnr-cart"></span></a>
                  </div> -->
              </div>
              
              <div class="quantity_progress">
                  <p class="product_sold">Sold: <span>{{data.sold_qty}} / {{data.total_qty}}</span></p>
                  <!-- <p class="product_available">Availabe: <span>9800</span></p> -->
              </div>
              <!-- <div class="bar_percent" id="bar_percent">

              </div> -->
              <div>
                <timer 
                  :starttime = startDate
                  :endtime = endDate
                  :servertime="serverDate"
                  trans='{  
                  "day":"Day",
                  "hours":"Hours",
                  "minutes":"Minutes",
                  "seconds":"Seconds",
                  "expired":"Event has been expired.",
                  "running":"Hurry Up! Offer ends in:",
                  "upcoming":"Flash sale starts in:",
                  "status": {
                      "expired":"Expired",
                      "running":"Running",
                      "upcoming":"Future"
                    }}'
                  ></timer>
              </div>
          </div>
      </div>
  </router-link>
</template>

<script>
import helper from '@/helper/global.js'
import Vue from 'vue'

export default {
  props: {
    data: {},
    /*  Format
        data: {
            title: String,
            category: String,
            discount: Int,
            rating: Int,
            old_price: Int,
            current_price: Int,
            image: String,
            sold_qty: Int,
            total_qty: Int
        }
    */
  },
  data () {
    return {
      imageHeight: 0
    }
  },
  computed:{
    startDate(){
      console.log("START DATE",Vue.moment(this.data.start_time).format('MMM DD, YYYY hh:mm:ss'))
      return Vue.moment(this.data.start_time).format('MMM DD, YYYY hh:mm:ss')
    },
    endDate(){
      console.log("END DATE",Vue.moment(this.data.end_time).format('MMM DD, YYYY hh:mm:ss'))
      return Vue.moment(this.data.end_time).format('MMM DD, YYYY hh:mm:ss')
    },
    serverDate() {
      console.log("SERVER DATE",Vue.moment(this.data.server_time).format('MMM DD, YYYY hh:mm:ss'))
      return Vue.moment(this.data.server_time).format('MMM DD, YYYY hh:mm:ss')
    }
  },
  created () {
    window.addEventListener('resize', this.resize)


  },
  mounted () {
    this.resize()
  },
  methods: {
    resize() {
      this.imageHeight = helper.resizeElement('productImageFlash', 1)
    }
  }
}
</script>