<template>
    <!--Side bar Choose Delivery-->
    <div :class="$store.state.sidebar.addAddress ? 'active' : ''" class="mini_cart">
        <div class="cart_close mb-1">
            <div class="cart_text">
                <h3>{{ isEdit ? 'Edit' : 'Add' }} Address</h3>
            </div>
            <div class="mini_cart_close" @click="$store.state.sidebar.addAddress = false">
                <a href="javascript:void(0)"><i class="fa fa-close"></i></a>
            </div>
        </div>
        <div class="mb-5">
            <div class="row mx-1">
                <div class="login_form_container">
                    <div class="account_login_form mt-3">
                        <form action="#">
                            <label class="mt-0">Recipient</label>
                            <input type="text" name="recipient" v-model="addressForm.recipient_name">
                            <label>Recipient's Phone</label>
                            <input type="number" name="recipient_phone" v-model="addressForm.phone">
                            <label>Save Address as</label>
                            <input type="text" name="address_type" v-model="addressForm.address_title">
                            <label>Address</label>
                            <!-- <input type="text" name="address" v-model="address.address_line1"> -->
                            <textarea name="address" rows="3" v-model="addressForm.address_line1"/>
                            <label>Search Location</label>
                            <vue-autosuggest
                                :suggestions="suggestions"
                                v-model="query"
                                :inputProps="{placeholder:'Search Your Location'}"
                                :sectionConfigs="sectionConfigs"
                                :renderSuggestion="renderSuggestion"
                                :getSuggestionValue="getSuggestionValue"
                                @input="fetchResults"
                            />

                            <!-- <input type="text" name="city" v-model="addressForm.city"> -->
                            <label v-if="addressForm.subdistrict != '' && addressForm.subdistrict != null">Subdisctrict</label>
                            <input v-if="addressForm.subdistrict != '' && addressForm.subdistrict != null" type="text" name="district" v-model="addressForm.district" disabled>
                            <label v-if="addressForm.district != '' && addressForm.district != null">Disctrict</label>
                            <input v-if="addressForm.district != '' && addressForm.district != null" type="text" name="district" v-model="addressForm.district" disabled>
                            <label v-if="addressForm.city != ''">City</label>
                            <input v-if="addressForm.city != ''" type="text" name="city" v-model="addressForm.city" disabled>
                            <label v-if="addressForm.state != ''">Province</label>
                            <input v-if="addressForm.state != ''" type="text" name="province" v-model="addressForm.state" disabled>
                            <label v-if="addressForm.country != ''">Country</label>
                            <input v-if="addressForm.country != ''" type="text" name="country" v-model="addressForm.country" disabled>
                            <label>Postal Code</label>
                            <input type="number" name="postal_code" v-model="addressForm.pincode">
                            <!-- <br/> -->
                            <p-check class="p-tada p-icon p-round mb-20" style="font-size: 16px" v-model="addressForm.is_default">
                                <i class="icon fa fa-check" slot="extra"></i>
                            </p-check>
                            <label>Set as default address</label>
                            <!-- <p-check class="p-switch p-fill mb-20" style="font-size: 16px" v-model="addressForm.is_default">{{ addressForm.is_default == 0 ? 'No' : 'Yes' }}</p-check> -->
                            <div class="save_button primary_btn default_button mt-2">
                                <a class="button" @click="saveAddress"><b>Save</b></a>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </div>
    </div>
    <!--Sidebar Choose Delivery end-->
</template>

<script>
import apiRequest from '@/helper/apiRequest.js'

export default {
    data () {
        return {
            chosenAddress: -1,
            isEdit: false,
            addressForm: {
                address_title: '',
                recipient_name: '',
                address_line1: '',
                city: '',
                state: '',
                country: '',
                pincode: '',
                jne_id: '',
                phone: '',
                district: '',
                subdistrict: '',
                is_default: 0
            },
            callback: () => {},

            query: '',
            suggestions: [
                {
                    name: "Search Result",
                    data: []
                }
            ],
            sectionConfigs: {
                Product: {
                    label: "Search Result",
                    onSelected: selected => {
                        console.log('onclick', selected.item.origin_name)
                        this.getSuggestionValue(selected.item)
                    }
                },
                default: {
                    onSelected: selected => {
                        this.getSuggestionValue(selected.item)
                    }
                }
            },
        }
    },
    created () {
        
    },
    mounted () {
        console.log('mounted')
    },
    watch: {
        '$store.state.sidebarData': function (newVal) {
            if (newVal) {
                Object.assign(this.addressForm, newVal)
                this.query = newVal.city

                this.isEdit = true
            } else {
                this.resetForm()
                this.isEdit = false
            }
        }
    },
    methods: {
        saveAddress () {
            this.callback = this.$store.state.sidebarCallback
            
            var body = this.addressForm
            body['doctype'] = "Address"
            body['city_delivery'] = body['city']
            body['district_delivery'] = body['district']
            body['subdistrict_delivery'] = body['subdistrict']
            body['user'] = this.$store.state.email

            this.saveOrEditAddress(body)
        },
        async saveOrEditAddress (address) {
            // after add / edit do close sidebar
            this.$store.dispatch('openSideBar', {
                'sidebar': 'addAddress',
                'open': false,
                'callback': () => {}
            })

            this.$store.state.showLoading = true

            let response = await apiRequest.createOrUpdateDoc(address)
            if (response.data == null) {
                this.$notify({
                    group: 'notification',
                    type: 'danger',
                    title: 'Oops...',
                    text: response.error
                });

                this.$store.dispatch('openSideBar', {
                    'sidebar': 'addAddress',
                    'open': true,
                    'callback': this.callback
                })
            } 
            else {
                this.callback()
                // notify succession
                this.$notify({
                    group: 'notification',
                    type: 'success',
                    title: 'Yey...',
                    text: "account.addresses.successAddAddress"
                });
                this.resetForm()
            }
            this.$store.state.showLoading = false
        },
        async fetchResults() {
            var body = {
                "identifier" : "Get Search Territory",
                "search" : this.query
            }
            var response = await apiRequest.requestRead(body)
            this.suggestions = []
            var datas = []
            for( let i in response.data ) {
                datas.push(response.data[i])
            }
            if(response.data != null){
                this.suggestions.push({ "name": "Product","data": datas})
            }
        },
        renderSuggestion(suggestion) {
            if (suggestion.name == "Product") {
                const data = suggestion.item;
                if(data.type == "District"){
                    return (
                    <div style="font-weight: 600; text-overflow: ellipsis; overflow: hidden; white-space: nowrap;">
                        {data.district},{data.city},{data.province},{data.country}
                    </div>
                    );
                }
                else{
                    return (
                    <div style="font-weight: 600; text-overflow: ellipsis; overflow: hidden; white-space: nowrap;">
                        {data.subdistrict},{data.district},{data.city},{data.province},{data.country}
                    </div>
                    );
                }
            } else {
                return ''
            }
        },
        getSuggestionValue (suggestion) {
            console.log(suggestion)
            const data = suggestion.item;
            this.addressForm.state = suggestion.item.province
            this.addressForm.country = suggestion.item.country
            this.addressForm.city = suggestion.item.city
            this.addressForm.district = suggestion.item.district
            this.addressForm.subdistrict = suggestion.item.subdistrict
            if (suggestion.item.type == "District"){
                return data.district + ", " + data.city + ", " + data.province + ", " + data.country
            }
            else {
                return data.subdistrict + ", " + data.district + ", " + data.city + ", " + data.province + ", " + data.country
            }
        },
        resetForm() {
            this.addressForm = {
                address_title: '',
                recipient_name: '',
                address_line1: '',
                city: '',
                state: '',
                country: '',
                pincode: '',
                jne_id: '',
                phone: '',
                district: '',
                is_default: 0
            }
            this.query = ''
        }
    }
}
</script>